import { ChangeDetectorRef, Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { CompositeGroupControl, isCompositeGroupControl } from '../../models/template/control';
import { FormArray, FormGroup, UntypedFormArray, UntypedFormGroup } from '@angular/forms';
import { Step } from '../../models/template';
import { DynamicFormService } from '../../services/dynamic-form.service';

@Component({
  selector: 'theia-composite-group',
  templateUrl: './composite-group.component.html',
  styleUrls: ['./composite-group.component.scss']
})
export class CompositeGroupComponent implements OnInit, OnChanges {

  @Input() group!: CompositeGroupControl;
  @Input() parentForm: FormGroup = new UntypedFormGroup({});
  @Input() step: Step | undefined;
  @Input() id: string = "";

  protected readonly isCompositeGroupControl = isCompositeGroupControl;


  get arrayForm(): FormArray<UntypedFormGroup> {
    return this.parentForm.controls[this.group.id] as FormArray<UntypedFormGroup>;
  }

  getGroupControls(form: FormGroup) {
    return (form as UntypedFormGroup & {groupControls: any[]}).groupControls || this.group.controls;
  }

  constructor(private dynamicForm: DynamicFormService, private cdr: ChangeDetectorRef) {
  }

  ngOnInit(): void {
    this.init();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.id.currentValue !== changes.id.previousValue) {
      this.init();
    }
  }

  init(): void {
    const formArray: UntypedFormArray = this.parentForm.get(this.group.id) as UntypedFormArray;
    const form: UntypedFormGroup = new UntypedFormGroup({});
    if (formArray?.length === 0) {
      const controls = this.group.controls.map(control => ({...control}));
      this.dynamicForm.initialize(form, controls, this.cdr, {});
      (form as UntypedFormGroup & {groupControls: any[]}).groupControls = controls;
      formArray?.push(form);
    } else {
      formArray.controls.forEach((group) => {
        const controls = this.group.controls.map(control => ({...control}));
        this.dynamicForm.initialize(group as FormGroup, controls, this.cdr, {});
        (group as UntypedFormGroup & {groupControls: any[]}).groupControls = controls;
      });
    }
  }

  add(): void {
    const form = new UntypedFormGroup({});
    const controls = this.group.controls.map(control => ({...control}));
    (form as UntypedFormGroup & {groupControls: any[]}).groupControls = controls;
    this.dynamicForm.initialize(form, controls, this.cdr, {});
    (this.parentForm.get(this.group.id) as UntypedFormArray)?.push(form);
  }

  delete(i: number) {
    (this.parentForm.get(this.group.id) as UntypedFormArray)?.removeAt(i);
  }

}
