import { Component, Input, OnInit } from '@angular/core';
import { DataSourceService } from '../../../../services/data-source.service';
import { PanelTable } from '../../../../models/template/dashboard';
import { DataSourceResponse } from '../../../../models/data-source-response';

@Component({
  selector: 'theia-panel-table',
  templateUrl: './panel-table.component.html',
  styleUrls: ['./panel-table.component.scss']
})
export class PanelTableComponent implements OnInit {
  @Input() panel!: PanelTable;

  data: DataSourceResponse[] = [];
  loading: boolean = true;

  constructor(private dataSourceService: DataSourceService) {

  }

  ngOnInit() {
    this.dataSourceService.getData<DataSourceResponse[]>(this.panel.datasource, this.panel.env_required)
      .subscribe((data) => {
        this.loading = false;
        this.data = data;
      });
  }

  getValues(row: DataSourceResponse): string[] {
    return this.panel.columns!.map(column => row[column.key]);
  }

}
