import { Either } from './util';

export interface Row {
  columns: Column[];
}

export type Column = Either<ColumnWithRows, ColumnWithPanel> & BaseColumn;

export interface BaseColumn {
  size?: number;
}

export interface ColumnWithRows {
  rows: Row[];
}

export interface ColumnWithPanel {
  panel: PanelMarkdown | PanelTable;
}

export interface Panel {
  title: string;
  type: PanelType;
}

export interface PanelMarkdown extends Panel {
  type: PanelType.Markdown;
  content: string;
}

export interface PanelTable extends Panel {
  type: PanelType.Table;
  datasource: string;
  description?: string;
  columns?: {key: string; label: string;}[];
  env_required?: boolean;
}

export enum PanelType {
  Markdown = 'Theia::Panel::Markdown',
  Table = 'Theia::Panel::Table',
}
