import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { DataSourceResponse } from '../../models/data-source-response';
import { Step } from '../../models/template';
import { PaginationResponse } from '../../models/pagination-response';
import { SortOrder } from 'src/app/models/grid';

export interface GridEvent {
  isNew: boolean;
  dynamicStep?: Step;
  data?: Record<string, string>;
}

@Component({
  selector: 'theia-grid', templateUrl: './grid.component.html', styleUrls: ['./grid.component.scss'],
})
export class GridComponent implements OnInit {

  @Input() step: Step | undefined;
  @Input() formGroup: UntypedFormGroup = new UntypedFormGroup({});
  @Input() data: DataSourceResponse[] = [];
  @Input() loading: boolean = false;
  @Input() paginationData?: PaginationResponse;
  @Input() sortKey: string = '';
  @Input() sortOrder: SortOrder = SortOrder.Asc;
  @Input() selectedRowId: string = '';

  @Output() selected: EventEmitter<GridEvent> = new EventEmitter<GridEvent>();
  @Output() sort: EventEmitter<string> = new EventEmitter<string>();
  @Output() pageChange: EventEmitter<number> = new EventEmitter<number>();
  SortOrder = SortOrder;

  constructor() {
  }

  get columns(): string[] {
    return this.step?.columns || [];
  }

  ngOnInit(): void {
    this.formGroup.reset();
  }

  getHeaders(row: DataSourceResponse): string[] {
    return Object.keys(row);
  }

  getValues(row: DataSourceResponse): string[] {
    return this.columns.map(column => row[column]);
  }

  sortBy(column: string): void {
    this.sort.emit(column);
  }

  onPageChange(page: number) {
    this.pageChange.emit(page);
  }

  onRowSelected(row: DataSourceResponse) {
    this.selected.emit({
      isNew: false, data: row
    });
  }

  isLink(value: string) {
    return /^https?:\/\/[^ ]+$/.test(value);
  }
}
