import {
  AfterViewInit,
  Component,
  ContentChild,
  ContentChildren,
  ElementRef,
  EventEmitter,
  OnInit,
  Output,
  QueryList,
  TemplateRef,
  ViewChild,
  ViewContainerRef
} from '@angular/core';
import { DynamicControlComponent } from '../../../dynamic-control/dynamic-control.component';

@Component({
  selector: 'theia-accordion-item',
  templateUrl: './accordion-item.component.html',
  styleUrls: ['./accordion-item.component.scss']
})
export class AccordionItemComponent implements OnInit, AfterViewInit {

  collapsed: boolean = true;
  visible: boolean = false;

  collapsing: boolean = false;
  @Output() collapse: EventEmitter<void> = new EventEmitter<void>();
  @Output() expand: EventEmitter<void> = new EventEmitter<void>();
  @ViewChild('template', {static: true}) template!: TemplateRef<unknown>;
  @ViewChild('accordionTop') top!: ElementRef<HTMLInputElement>;
  @ContentChild('accordionHeader') header?: DynamicControlComponent;
  @ContentChildren('accordionControl') children?: QueryList<DynamicControlComponent>;

  constructor(private viewContainerRef: ViewContainerRef) {
  }

  ngOnInit(): void {
    this.viewContainerRef.createEmbeddedView(this.template);
  }

  toggleItem(event: MouseEvent) {
    event.stopPropagation();
    this.collapsed = !this.collapsed;
    if (this.collapsed) {
      this.collapse.emit();
    } else {
      this.expand.emit();
      setTimeout(() => {
        this.top.nativeElement.scrollIntoView({ behavior: "smooth"});
      }, 0);
    }
  }

  setChildrenReadOnly(readOnly: boolean) {
    this.children?.forEach((item) => {
      if (readOnly) {
        item.ngControl!.control?.disable({emitEvent: false, onlySelf: true});
      } else {
        item.ngControl!.control?.enable({emitEvent: false, onlySelf: true});
      }
    });
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.setChildrenReadOnly(!this.header?.value);
    });
  }

  onVisible(visible: boolean) {
    this.visible = visible;
  }
}
