import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnDestroy, Output, ViewChild } from '@angular/core';
import { debounceTime, distinctUntilChanged, filter, map } from 'rxjs/operators';
import { fromEvent, Subscription } from 'rxjs';

@Component({
  selector: 'theia-type-ahead',
  templateUrl: './type-ahead.component.html',
  styleUrls: ['./type-ahead.component.scss']
})
export class TypeAheadComponent implements AfterViewInit, OnDestroy {

  @Input() value?: string;
  @Input() placeHolder?: string;
  @Input() minLength = 3;

  @Output() valueChange = new EventEmitter<string>();

  @ViewChild('input', { static: true }) input!: ElementRef;

  private buttonSubscription$?: Subscription;
  ngAfterViewInit(): void {

    this.buttonSubscription$ = fromEvent<Event>(this.input.nativeElement, 'input')
      .pipe(
        debounceTime(200),
        map(event => (<HTMLInputElement>event.target)?.value),
        distinctUntilChanged(),
        filter(value => value?.length === 0 || value?.length >= this.minLength),
      )
      .subscribe((event) => {
        this.valueChange.emit(event);
      });
  }

  ngOnDestroy(): void {
    this.buttonSubscription$?.unsubscribe();
  }
}
