import {Component, Inject, OnInit} from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Environment, EnvironmentTree } from '../../models/environment';
import { EnvironmentService } from '../../services/environment.service';
import { AuthService, User } from '@auth0/auth0-angular';
import { ICustomWindow, WindowRefService } from '../../services/window-ref.service';
import { LogoutService } from '../../services/logout.service';
import { finalize } from 'rxjs/operators';
import { CloudService } from '../../services/cloud.service';
import { CloudInformation, CloudsResponse } from '../../models/clouds-response';
import { APP_BASE_HREF } from '@angular/common';
import { AllowedAppsService } from '../../services/allowed-apps.service';
import { AllowedApp, AllowedAppsResponse } from '../../models/allowed-app';

@Component({
  selector: 'theia-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {
  collapsed = true;
  currentEnv$: BehaviorSubject<Environment | undefined>;
  environments$: BehaviorSubject<Environment[] | undefined>;
  user$: Observable<User | null | undefined>;
  environmentsTree$: BehaviorSubject<EnvironmentTree[]>;
  private window: ICustomWindow;
  public selectedCloud$: BehaviorSubject<string>;
  public selectedApp$: BehaviorSubject<string>;
  public availableClouds$: BehaviorSubject<CloudsResponse>;
  public allowedApps$: BehaviorSubject<AllowedAppsResponse>;
  public allowedAWSApps$: BehaviorSubject<(AllowedApp  & {id: string;})[]> = new BehaviorSubject<(AllowedApp & {id: string})[]>([]);
  public allowedAzureApps$: BehaviorSubject<(AllowedApp  & {id: string;})[]> = new BehaviorSubject<(AllowedApp & {id: string})[]>([]);
  public allowedGCPApps$: BehaviorSubject<(AllowedApp  & {id: string;})[]> = new BehaviorSubject<(AllowedApp & {id: string})[]>([]);
  public recentEnvironments$: BehaviorSubject<Environment[] | undefined> = new BehaviorSubject<Environment[] | undefined>(undefined);
  get selectedCloud(): CloudInformation {
    return this.availableClouds$.value[this.selectedCloud$.value];
  }

  get selectedApp(): AllowedApp | undefined {
    return this.allowedApps$.value[this.selectedApp$.value];
  }

  constructor(private environmentService: EnvironmentService,
              private cloudService: CloudService,
              private allowedAppsService: AllowedAppsService,
              private authService: AuthService,
              private windowRef: WindowRefService,
              private logoutService: LogoutService,
              @Inject(APP_BASE_HREF) private baseHref: string) {
    this.currentEnv$ = environmentService.getCurrentEnvironment();
    this.selectedCloud$ = cloudService.getSelectedCloud();
    this.selectedApp$ = allowedAppsService.getSelectedApp();
    this.recentEnvironments$ = environmentService.getRecentEnvironments();
    this.environments$ = environmentService.environments$;
    this.environmentsTree$ = environmentService.environmentsTree$;
    this.user$ = authService.user$;
    this.window = windowRef.nativeWindow;

    this.availableClouds$ = cloudService.availableClouds$;
    this.allowedApps$ = allowedAppsService.allowedApps$;
  }

  ngOnInit() {
    this.allowedAWSApps$.next(this.getAllowedAppsForCloud('aws'))
    this.allowedAzureApps$.next(this.getAllowedAppsForCloud('azure'))
    this.allowedGCPApps$.next(this.getAllowedAppsForCloud('gcp'))
  }

  toggle(): void {
    this.collapsed = !this.collapsed;
  }

  selectEnv(event: MouseEvent, env: Environment | undefined): void {
    if (!env) {
      return;
    }
    event.preventDefault();
    this.environmentService.setCurrentEnvironment(env);
  }

  selectCloud(cloud: string): void {
    if (!cloud) {
      return;
    }
    this.cloudService.setSelectedCloud(cloud);
  }

  selectApp(event: MouseEvent, app: string): void {
    if (!app) {
      return;
    }
    event.preventDefault();
    this.allowedAppsService.setSelectedApp(app);
    this.selectCloud(this.allowedApps$.value[app].cloud);
  }

  getEntries(obj: object | null): {key: string, value: object}[] {
    if (!obj) {
      return [];
    }
    return Object.entries(obj)
      .map(e => ({key: e[0], value: e[1]}));
  }

  getEnvFromValue(value: object): Environment {
    return value as Environment;
  }

  getAllowedAppsForCloud(cloud: string): (AllowedApp  & {id: string;})[] {
    return Object.entries(this.allowedApps$.value)
      .reduce((previousValue: (AllowedApp  & {id: string;})[], [currentKey, currentValue]) => {
        if (currentValue.cloud === cloud) {
          previousValue.push({
            id: currentKey,
            ...currentValue
          });
        }
        return previousValue;
      }, []);
  }

  logout(e: MouseEvent) {
    e.preventDefault();
    this.logoutService.logout()
      .pipe(
        finalize(() => {
          this.authService.logout({
            logoutParams: {
              returnTo: this.window.location.origin + this.baseHref.replace(/\/$/, '')
            }
          });
        })
      ).subscribe();
  }

}
