import { Component, Input, OnInit } from '@angular/core';
import { switchMap } from 'rxjs/operators';
import { CommandResponse } from '../../models/command-response';
import { Diagram } from '../../models/diagram/diagram';
import { Icon } from '../../models/diagram/icon';
import { Command } from '../../models/template/command';
import { WizardOption } from '../../models/wizard-option';
import { CommandService } from '../../services/command.service';
import { EnvironmentService } from '../../services/environment.service';
import { InfoService } from '../../services/info.service';
import { LogService } from '../../services/log.service';
import { TemplateService } from '../../services/template.service';
import { DynamicComponent } from '../../models/dynamic-component';

@Component({
  selector: 'theia-diagram-options',
  templateUrl: './architecture-wizard.component.html',
  styleUrls: ['./architecture-wizard.component.scss']
})
export class ArchitectureWizardComponent implements OnInit, DynamicComponent {

  @Input() data?: {
    id: string;
    endpoint: string;
    info: string;
    command: Command
  };
  options: WizardOption[] = [];

  diagram: Diagram = {
    cellSize: 20,
    rows: 30,
    columns: 36,
    grid: false,
    icons: [],
    links: []
  };

  submitting = false;
  log = '';
  showLog = false;
  showError = false;
  currentOption = '';
  showCommandsModal = false;
  selectedCommands: string[] = [];

  constructor(
    private templateService: TemplateService,
    private commandService: CommandService,
    private logService: LogService,
    private environmentService: EnvironmentService,
    private infoService: InfoService) {
  }

  ngOnInit(): void {
    const match = (this.data?.id || '').match(/^(aws|azure|gcp)_([^_]+)_wizard$/);
    // const match = (this.data?.id || '').match(/^aws_([^_]+)_wizard$/);
    console.log(match);
    // OLD
    // 0: "aws_solution_wizard"
    // 1: "solution"
    //
    // NEW
    // 0: "aws_solution_wizard"
    // 1: "aws"
    // 2:"solution"    
    if (!match || !match.length) {
      return;
    }
    const endpoint = match[2];
    this.templateService.getDiagramData<WizardOption[]>(`wizard/${endpoint}`, [])
      .pipe(
        switchMap((data) => {
          this.options = data;
          this.infoService.data = this.data?.info || '';
          return this.templateService.getDiagramData<Diagram>(this.data?.endpoint || '', this.diagram)
        })
      )
      .subscribe((diagram) => {
        diagram.icons = diagram.icons.map((icon) => {
          icon.display = false;
          delete icon.active;
          return icon;
        });
        diagram.cellSize = 23;
        this.diagram = diagram as Diagram;
        this.optionChanged();
      });
  }

  optionChanged(): void {
    const selectedIds = new Set();
    const highlightedActions: Set<string> = new Set();

    const getSelectedIs = (options: WizardOption[]) => {
      options.forEach((option) => {
        if (option.checked) {
          option.ids.forEach((id) => selectedIds.add(Number(id)));
          (option.actions || []).forEach((id) => highlightedActions.add(id));
        }

        if (option.children?.length) {
          getSelectedIs(option.children);
        }
      });
    };

    getSelectedIs(this.options);
    this.diagram.icons.forEach((icon) => {
      icon.display = [...selectedIds].includes(Number(icon.id));
    });
    this.environmentService.highlightedActions$.next([...highlightedActions]);
  }

  getIconById(id: number): Icon | undefined {
    return this.diagram.icons.find((icon) => Number(icon.id) === Number(id));
  }

  onOptionChange(event: Event, option: WizardOption): void {
    const target = event.currentTarget as HTMLInputElement;
    if (!target.checked && option.children?.length) {
      this.disableChildren(option.children);
    }
    this.optionChanged();
  }

  disableChildren(options: WizardOption[]): void {
    options.forEach((option) => {
      option.checked = false;
      if (option.children?.length) {
        this.disableChildren(option.children);
      }
    });
  }

  sendPayload(): void {
    const payload = {
      ...this.data?.command.command,
      wizard: this.options,
    };

    this.submitting = true;

    this.commandService.runCommand(payload)
      .pipe(
        switchMap((resp: CommandResponse) => this.logService.getLog(resp.log_file))
      )
      .subscribe((log) => {
        this.log = log;
        this.showLog = true;
        this.submitting = false;
      }, () => {
        this.submitting = false;
        this.showError = true;
      });
  }

  modalClose(): void {
    this.showLog = false;
    this.showError = false;
  }

  showCommands(option: WizardOption): void {
    this.showCommandsModal = true;
    this.selectedCommands = option.cli as string[];
  }

  onMouseEnter(option: WizardOption, id: string): void {
    if (!option.cli || !option.cli.length) {
      return;
    }
    this.currentOption = id;
  }

  onMouseLeave(): void {
    this.currentOption = '';
  }

  commandsModalClose(): void {
    this.showCommandsModal = false;
    this.selectedCommands = [];
  }
}
