import { Component, OnInit } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Template } from './models/template';
import { TemplateService } from './services/template.service';
import { AuthService } from '@auth0/auth0-angular';
import { CloudService } from './services/cloud.service';
import { ICustomWindow, WindowRefService } from './services/window-ref.service';
import { distinctUntilChanged, skip, switchMapTo } from 'rxjs/operators';
import { Router } from '@angular/router';
import { environment } from '../environments/environment';
import { AllowedAppsService } from './services/allowed-apps.service';
import { EnvironmentService } from './services/environment.service';
import { Environment } from './models/environment';

@Component({
  selector: 'theia-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {

  template$: BehaviorSubject<Template | undefined | null>;
  environments$: BehaviorSubject<Environment[] | undefined> = new BehaviorSubject<Environment[] | undefined>(undefined);
  error: Observable<Error>;

  private window: ICustomWindow;

  constructor(private templateService: TemplateService,
              private cloud: CloudService,
              private app: AllowedAppsService,
              private authService: AuthService,
              private environment: EnvironmentService,
              private router: Router,
              private windowRef: WindowRefService) {
    this.template$ = templateService.template$;
    this.error = authService.error$;
    this.window = windowRef.nativeWindow;
    this.environments$ = environment.environments$;
  }

  ngOnInit(): void {

    const appChanged = this.app.getSelectedApp()
      .pipe(skip(1),
        distinctUntilChanged());

    this.app.getAllowedApps()

    this.cloud.getClouds()
      .pipe(
        switchMapTo(this.app.getAllowedApps()),
        switchMapTo(appChanged)
      )
      .subscribe(() => {
        this.window.location.href = environment.redirectUri;
      });
  }
}
