import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { environment } from '../../environments/environment';
import { Environment, EnvironmentTree } from '../models/environment';
import { WizardOption } from '../models/wizard-option';
import { LocalStorageService } from './local-storage.service';
import { AllowedAppsService } from './allowed-apps.service';

type EnvironmentMap =  Record<string, {
  selected?: string;
  recent?: string[];
}>;

@Injectable({
  providedIn: 'root'
})
export class EnvironmentService {

  currentEnvironment$: BehaviorSubject<Environment | undefined> = new BehaviorSubject<Environment | undefined>(undefined);
  environments$: BehaviorSubject<Environment[] | undefined> = new BehaviorSubject<Environment[] | undefined>(undefined);
  highlightedActions$: BehaviorSubject<string[]> = new BehaviorSubject<string[]>([]);

  environmentsTree$: BehaviorSubject<EnvironmentTree[]> = new BehaviorSubject<EnvironmentTree[]>([]);
  recentEnvironments$: BehaviorSubject<Environment[] | undefined> = new BehaviorSubject<Environment[] | undefined>(undefined);


  constructor(private http: HttpClient, private localStorage: LocalStorageService, private app: AllowedAppsService) {
  }

  getEnvironments(): Observable<Environment[]> {
    return this.http
      .get<Environment[]>(`${environment.apiUrl}/data?type=profile`)
      .pipe(
        tap((response) => {
          this.environments$.next(response);
          this.environmentsTree$.next(this.generateEnvironmentsTree(response));
        })
      );
  }

  getSelectedEnvironment() {
    return this.getEnvironmentByName(this.loadEnvironmentMap()[this.app.selectedApp$.value]?.selected ?? "");
  }

  getCurrentEnvironment(): BehaviorSubject<Environment | undefined> {
    if (!this.currentEnvironment$.getValue()) {
      const env = this.getSelectedEnvironment();
      if (env) {
        this.currentEnvironment$.next(env);
      }
    }
    this.setHighlightedActions();
    return this.currentEnvironment$;
  }

  loadEnvironmentMap(): EnvironmentMap {
    const envMap = this.localStorage.getItem('environmentData') ?? "{}";
    try {
      return JSON.parse(envMap);
    } catch (e) {
      return {};
    }
  }

  getRecentEnvironments(): BehaviorSubject<Environment[] | undefined> {
    if (!this.recentEnvironments$.getValue()) {
      const data = this.loadEnvironmentMap();
      const app = data[this.app.selectedApp$.value] || {};
      const recent = (app.recent || []).map(e => this.getEnvironmentByName(e)!);
      this.recentEnvironments$.next(recent);
    }
    return this.recentEnvironments$;
  }

  setCurrentEnvironment(env: Environment): void {
    const data = this.loadEnvironmentMap();
    const recent = new Set(data[this.app.selectedApp$.value]?.recent || []);
    if (recent.has(env.name)) {
      recent.delete(env.name);
    }
    data[this.app.selectedApp$.value] = {
      selected: env.name,
      recent: [env.name, ...recent].slice(0, 6)
    };
    this.localStorage.setItem('environmentData', JSON.stringify(data));
    this.currentEnvironment$.next(env);
    this.recentEnvironments$.next(data[this.app.selectedApp$.value].recent!.map(e => this.getEnvironmentByName(e)!));
    this.setHighlightedActions();
  }

  getEnvironmentByName(name: string): Environment | undefined {
    return this.environments$.value?.find((env) => env.name === name);
  }

  setHighlightedActions(): void {
    const getActiveActions = (options?: WizardOption[], actions: string[] = []): string[] => {
      if (options && options.length) {
        let result = [...actions];
        for (const option of options) {
          if (option.checked && option.actions) {
            result = [...result, ...option.actions, ...getActiveActions(option.children)];
          }
        }
        return result;
      } else {
        return actions;
      }
    }
    this.highlightedActions$.next(getActiveActions(this.currentEnvironment$.value?.wizard));
  }

  generateEnvironmentsTree(environments: Environment[]): EnvironmentTree[] {

    const tree = environments.reduce((prev: {[key: string]: {
        [key: string]: {
          [key: string]: Environment;
        }
      }}, curr) => {
      if (!prev[curr.client]) {
        prev[curr.client] = {};
      }
      if (!prev[curr.client][curr.workload]) {
        prev[curr.client][curr.workload] = {};
      }
      prev[curr.client][curr.workload][curr.env] = curr;
      return prev;
    }, {});


    return Object.entries(tree).map(([key, value]) => ({
      name: key,
      children: Object.entries(value).map(([key, value]) => ({
        name: key,
        children: Object.entries(value).map(([key, value]) => ({
          name: key,
          env: value
        }))
      }))
    }));
  }
}
