import {AfterViewInit, Directive, ElementRef, Input, OnDestroy, Renderer2} from '@angular/core';
import {SSEService} from "../services/sse.service";
import {Subscription} from "rxjs";

@Directive({
  selector: '[theiaLogStream][dataSource]'
})
export class LogStreamDirective implements AfterViewInit, OnDestroy {

  @Input() dataSource: string | undefined;

  eventSource$?: Subscription;
  constructor(private sse: SSEService, private renderer: Renderer2, private element: ElementRef) { }

  ngAfterViewInit(): void {
    if (!this.dataSource) {
      throw new Error('Datasource not defined');
    }
    this.eventSource$ = this.sse.createEventSource(this.dataSource)
      .subscribe(this.handleLogEvent.bind(this));
  }

  ngOnDestroy() {
    this.eventSource$?.unsubscribe();
  }

  handleLogEvent(log: string) {
    const container = this.renderer.createElement("div");
    const text = this.renderer.createText(log);
    this.renderer.appendChild(container, text);
    this.renderer.appendChild(this.element.nativeElement, container);
  }

}
