import { ChangeDetectorRef, Component, Input, OnChanges, OnInit, Optional, Self, SimpleChanges } from '@angular/core';
import { ControlValueAccessor, NgControl, UntypedFormGroup, ValidationErrors } from '@angular/forms';
import { Control, ControlOption, Step, TheiaControl, TheiaValidation, Validation } from '../../models/template';
import { DataSourceService } from '../../services/data-source.service';
import { BehaviorSubject } from 'rxjs';
import { CommandService } from '../../services/command.service';
import { FileItem, FileUploader, FileUploaderOptions } from 'ng2-file-upload';
import { S3UploadService } from '../../services/s3-upload.service';
import * as mime from 'mime';
import {
  CommonControl,
  hasDatasource,
  isCodeControl,
  isComplexControl,
  isInputControl,
  isKeyValueControl,
  isMultiSelectControl,
  isSelectControl,
  isSelectOrMultiSelect,
  isTextAreaControl,
  isToggleControl,
  isUploadControl,
  MultiSelectControl,
  SelectControl
} from '../../models/template/control';

@Component({
  selector: 'theia-dynamic-control',
  templateUrl: './dynamic-control.component.html',
  styleUrls: ['./dynamic-control.component.scss']
})
export class DynamicControlComponent implements OnInit, OnChanges, ControlValueAccessor {

  @Input() control: Control | undefined;
  @Input() datasource: string | undefined = '';
  @Input() parentForm: UntypedFormGroup = new UntypedFormGroup({});
  @Input() step: Step | undefined;
  @Input() class: string = "";

  TheiaControl = TheiaControl;
  value: string | string[] | boolean | unknown = '';
  isDisabled = false;

  allSelected = false;

  keyValueString: string = '';

  loadingDataSource: boolean = false;

  isNew$: BehaviorSubject<boolean>;
  dropZoneOver = false;
  uploader?: FileUploader;

  protected readonly isInputControl = isInputControl;
  protected readonly isKeyValueControl = isKeyValueControl;
  protected readonly isToggle = isToggleControl;
  protected readonly isSelect = isSelectControl;
  protected readonly isMultiSelect = isMultiSelectControl;
  protected readonly isTextArea = isTextAreaControl;
  protected readonly isCodeControl = isCodeControl;
  protected readonly isUploadControl = isUploadControl;


  get valueAsString(): string {
    return this.value as string;
  }

  constructor(@Self() @Optional() public ngControl: NgControl,
              private cdr: ChangeDetectorRef,
              private dataSourceService: DataSourceService,

              private commandService: CommandService,
              private s3UploadService: S3UploadService
  ) {
    this.ngControl.valueAccessor = this;
    this.isNew$ = commandService.isNew$;
  }

  get invalid(): boolean {
    return !!((this.ngControl.touched || this.ngControl.dirty) && this.ngControl.invalid);
  }

  get disabled(): boolean {
    return !!this.ngControl.disabled;
  }

  get readonly(): boolean {
    return !!(this.control?.readonly || (this.control?.readonly_edit && !this.isNew$.value));
  }

  get validationErrors(): string[] {
    const errorTypes: { [key: string]: TheiaValidation; } = {
      email: TheiaValidation.Email,
      max: TheiaValidation.Max,
      min: TheiaValidation.Min,
      minlength: TheiaValidation.MinLength,
      maxlength: TheiaValidation.MaxLength,
      pattern: TheiaValidation.Pattern,
      required: TheiaValidation.Required,
      requiredtrue: TheiaValidation.RequiredTrue,
    };
    return Object
      .keys(this.ngControl.errors as ValidationErrors)
      .map((errorCode) => {
        const validation = this.findValidationByType(errorTypes[errorCode]);
        return validation?.message || '';
      });
  }

  get uploaderItems(): number {
    return this.uploader?.queue.length || 0;
  }

  ngOnInit() {
    if (!this.datasource && isSelectOrMultiSelect(this.control!)) {
      (this.control || {} as Control).options = this.groupIfMultiSelect(this.control?.options || []);
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.datasource && changes.datasource.currentValue) {
      if (isSelectOrMultiSelect(this.control!) && this.control?.dependency?.datasourceDependency && changes.datasource.firstChange) {
        (this.control || {} as Control).options = [];
      } else if (this.control?.type !== TheiaControl.Upload) {
        this.loadDatasource(changes.datasource.currentValue);
      }
    }
  }

  onChange = (_: string | boolean | unknown | undefined) => {
  };

  onTouch = () => {
  };

  writeValue(value: string | string[]): void {
    if (this.control?.type === TheiaControl.MultiSelect && typeof value === 'string') {
      value = value.split(',');
    }
    if (this.control?.type === TheiaControl.Upload) {
      const options: FileUploaderOptions  = {
        disableMultipart: true,
        method: 'PUT',
        url: ''
      };
      if (!this.control?.multiple) {
        options.queueLimit = 1;
      }
      this.uploader = new FileUploader(options);
      this.uploader.onSuccessItem = this.handleUploadSuccess.bind(this);
    }
    this.value = value;
    this.cdr.detectChanges();
  }

  registerOnChange(fn: () => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouch = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  handleInput(event: Event): void {
    this.value = (event.target as HTMLInputElement).value;
    this.onTouch();
    this.onChange(this.value);
  }

  findValidationByType(type: TheiaValidation): Validation | undefined {
    if (isComplexControl(this.control!)) {
      return;
    }
    return this.control?.validations?.find((validation) => validation.type === type);
  }

  handleToggleChange(event: Event): void {
    this.value = (event.target as HTMLInputElement).checked;
    this.onTouch();
    this.onChange(this.value);
  }

  handleBlur(): void {
    this.onTouch();
  }

  handleSelectChange(value: ControlOption): void {
    this.value = value;
    this.onTouch();
    this.onChange(this.value);
  }

  compare(item: ControlOption, selected: string): boolean {
    if (typeof item.value === 'string') {
      return item.value === selected;
    } else if (item.value) {
      return item.value.value === selected;
    }
    return false;
  }
  private groupIfMultiSelect(options: ControlOption[] = []): ControlOption[] {
    if (this.control?.type === TheiaControl.MultiSelect) {
      if (!Array.isArray(options)) {
        options= [];
      }
      return options.map(option => {
        (option as ControlOption & { theiaSelectAll: string; }).theiaSelectAll = 'all';
        return option;
      });
    }
    return options;
  }

  private loadDatasource(datasource: string): void {
    if (!hasDatasource(this.control!)) {
      return;
    }
    this.loadingDataSource = true;
    this.dataSourceService
      .getData<ControlOption[] | Record<string, string>>(datasource, !this.control?.env_not_required)
      .subscribe((response) => {
        const control =  (this.control || {} as Control);
        switch (this.control?.type) {
          case TheiaControl.MultiSelect:
          case TheiaControl.Select:
            (<CommonControl & (MultiSelectControl | SelectControl)>control).options = this.groupIfMultiSelect(response as ControlOption[]);
            break;
          case TheiaControl.KeyValue:
            this.value = '';
            this.keyValueString = '';
            this.cdr.detectChanges();
            this.value = JSON.stringify(response);
            this.keyValueString = this.value as string;
            break
          default:
            break;
        }
        this.loadingDataSource = false;
      }, () => this.loadingDataSource = false);
  }

  fileOver(event: boolean) {
    this.dropZoneOver = event;
  }

  uploadFile(fileItem: FileItem) {
    if (this.control?.type === TheiaControl.Upload) {
      fileItem.isError = false;
      const fileType: string = mime.getType(fileItem.file.name || '') || 'application/octet-stream';
      this.s3UploadService.getSignedUrl(this.control?.datasource, fileType, fileItem.file.name)
        .subscribe(response => {
          if (!response.length) {
            fileItem.isError = true;
            return;
          }
          fileItem.url = response[0].presigned_url;
          const additionalHeaders = Object
            .entries((this.control?.type === TheiaControl.Upload && this.control?.metadata) || {})
            .map(([name, value]) => ({
              name: `x-amz-meta-${name}`,
              value
            }));
          fileItem.headers = [
            {
              name: "Content-Type",
              value: fileType,
            },
            ...additionalHeaders
          ]

          fileItem.upload();
        }, error => {
          fileItem.isError = true;
        });
    }
  }

  uploadAll() {
    for (const fileItem of this.uploader?.queue || []) {
      if (!fileItem.isSuccess) {
        this.uploadFile(fileItem);
      }
    }
  }

  allUploaded() {
    return this.uploader?.queue.every(item => item.isSuccess);
  }

  handleUploadSuccess(item: FileItem) {
    if (!this.value) {
      this.value = [];
    }
    (this.value as string[]).push(item.url);
    this.onChange(this.value);
  }
}
