import { Component, OnInit } from '@angular/core';
import { Diagram } from '../../models/diagram/diagram';
import { Icon } from '../../models/diagram/icon';
import { Link } from '../../models/diagram/link';
import { Template } from '../../models/template';
import { InfoService } from '../../services/info.service';
import { TemplateService } from '../../services/template.service';

@Component({
  selector: 'theia-architecture',
  templateUrl: './architecture.component.html',
  styleUrls: ['./architecture.component.scss']
})
export class ArchitectureComponent implements OnInit {

  diagram: Diagram = {
    cellSize: 20,
    rows: 30,
    columns: 36,
    grid: false,
    icons: [],
    links: [],
  };

  constructor(
    private templateService: TemplateService, private infoService: InfoService) {
  }

  ngOnInit(): void {
    this.templateService
      .getDiagramData<Diagram>('diagram', this.diagram)
        .subscribe((result) => {
        this.diagram = result;
        this.diagram.links = this.orderByDisabled(this.diagram.links);
      });
    const template = this.templateService.template$.getValue() as Template;
    const section = template.sections
      .find((s) => s.id === 'architecture');
    if (section) {
      this.infoService.data = section.info || '';
    }
  }

  getIconById(id: number): Icon | undefined {
    return this.diagram.icons
      .find((icon) => icon.id === id);
  }

  isIconEnabled(id: number): boolean {
    const icon = this.getIconById(id);
    return !!(icon && icon.display);
  }

  isLinkDisabled(link: Link): boolean {
    return !this.isIconEnabled(link.to) || !this.isIconEnabled(link.from);
  }

  orderByDisabled(links: Link[] = []): Link[] {
    return links.sort((a, b) => Number(this.isLinkDisabled(b)) - Number(this.isLinkDisabled(a)));
  }

}
