import { AfterViewInit, Directive, ElementRef, Input } from '@angular/core';
import { Attributes, TheiaType } from '../models/template';

@Directive({
  selector: '[theiaControlAttributes]'
})
export class ControlAttributesDirective implements AfterViewInit {

  @Input() theiaControlAttributes: Attributes | undefined = {type: TheiaType.Attributes};

  constructor(private el: ElementRef) {
  }

  ngAfterViewInit(): void {
    for (const [key, value] of Object.entries(this.theiaControlAttributes || {})) {
      if (key === 'type') {
        return;
      }
      this.el.nativeElement.setAttribute(key, value);
    }
  }

}
