import { Component, OnInit } from '@angular/core';
import { TemplateService } from '../../services/template.service';
import { BehaviorSubject } from 'rxjs';
import { Environment } from '../../models/environment';
import { Action, Section } from '../../models/template';
import { EnvironmentService } from '../../services/environment.service';

@Component({
  selector: 'theia-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  currentEnv$: BehaviorSubject<Environment | undefined>;
  currentAction: Action | undefined;
  section: Section | undefined;

  constructor(private templateService: TemplateService, environmentService: EnvironmentService) {
    this.currentEnv$ = environmentService.getCurrentEnvironment();
  }

  ngOnInit(): void {
    this.currentAction = this.templateService
      .getActionById('home', 'dashboard');
  }

}
