import { Component, HostBinding, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { InfoService } from '../../services/info.service';

@Component({
  selector: 'theia-info',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.scss']
})
export class InfoComponent implements OnInit {

  data$: BehaviorSubject<string> = new BehaviorSubject<string>('');
  enabled$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  constructor(private infoService: InfoService) {
    this.data$ = infoService.data$;
    this.enabled$ = infoService.enabled$;
  }

  @HostBinding('class.collapsed') collapsed = false;
  @HostBinding('class.animating') animating = false;

  ngOnInit(): void {
    this.enabled$.subscribe(value => {
      this.animating = true;
      this.collapsed = !value;
      setTimeout(() => {
        this.animating = false;
      }, 250);
    });

  }

  close() {
    this.infoService.enabled = false;
  }

  openInfo() {
    this.infoService.enabled = true;
  }
}
