import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { environment } from '../../environments/environment';
import { EnvironmentService } from './environment.service';

@Injectable({
  providedIn: 'root'
})
export class DataSourceService {

  constructor(private http: HttpClient, private environmentService: EnvironmentService) {
  }

  getData<T>(endpoint: string = '', envRequired: boolean = false, params: Record<string, unknown> = {}): Observable<T> {
    const env = this.environmentService.getCurrentEnvironment().value?.name;
    if (envRequired && !env) {
      return throwError("No environment selected");
    }
    let queryParams = envRequired ? `&env=${env}` : '';
    for (const [param, value] of Object.entries(params)) {
      queryParams += `&${param}=${value}`;
    }
    return this.http.get<T>(`${environment.apiUrl}/${endpoint}${queryParams}`);
  }
}
