import {inject} from '@angular/core';
import {ActivatedRouteSnapshot, ResolveFn, RouterStateSnapshot} from '@angular/router';
import {of} from 'rxjs';
import {catchError} from 'rxjs/operators';
import {Environment} from '../models/environment';
import {EnvironmentService} from './environment.service';

export const environmentResolver: ResolveFn<Environment[]> = (route: ActivatedRouteSnapshot, state: RouterStateSnapshot) => {
  const environmentService = inject(EnvironmentService);

  if (environmentService.environments$.value?.length) {
    return environmentService.environments$.value;
  }
  return environmentService
    .getEnvironments()
    .pipe(catchError(() => of([])));
};
