import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { tap } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { AllowedAppsResponse } from '../models/allowed-app';
import { LocalStorageService } from './local-storage.service';

@Injectable({
  providedIn: 'root'
})
export class AllowedAppsService {
  allowedApps$: BehaviorSubject<AllowedAppsResponse> = new BehaviorSubject<AllowedAppsResponse>({});
  selectedApp$: BehaviorSubject<string> = new BehaviorSubject<string>("");

  constructor(private http: HttpClient, private localStorageService: LocalStorageService) { }

  getAllowedApps(): Observable<AllowedAppsResponse> {
    return this.http
      .get<AllowedAppsResponse>(`${environment.apiUrl}/allowed_apps`)
      .pipe(
        tap((response) => {
          this.allowedApps$.next(response);
        })
      );
  }

  getSelectedApp(): BehaviorSubject<string> {
    if (!this.selectedApp$.getValue()) {
      const selectedApp = this.localStorageService.getItem('selectedApp');
      if (selectedApp) {
        this.selectedApp$.next(selectedApp);
      } else {
        this.setSelectedApp('aws');
      }
    }
    return this.selectedApp$;
  }

  setSelectedApp(value: string) {
    this.selectedApp$.next(value);
    this.localStorageService.setItem('selectedApp', value);
  }
}
