import { Pipe, PipeTransform } from '@angular/core';

interface Data {
  [key: string]: unknown | Data;
}

@Pipe({
  name: 'removeNull'
})
export class RemoveNullPipe implements PipeTransform {

  transform(value: Data): object {
    return this.removeEmpty(value);
  }

  private removeEmpty(obj: Data): object {
    Object.keys(obj).forEach(key => {
      if (obj[key] && typeof obj[key] === 'object') {
        this.removeEmpty(obj[key] as Data);
      } else if (obj[key] === undefined || obj[key] === null) {
        delete obj[key];
      }
    });
    return obj;
  }

}
