import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { EnvironmentService } from './environment.service';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';

interface SignedUrlResponse {
  presigned_url: string;
}

@Injectable({
  providedIn: 'root'
})
export class S3UploadService {

  constructor(private http: HttpClient, private environmentService: EnvironmentService) { }

  getSignedUrl(endpoint: string = '', fileType: string = '', fileName: string = ''): Observable<SignedUrlResponse[]> {
    const env = this.environmentService.getCurrentEnvironment().value?.name;
    const params = `&env=${env}&file_name=${fileName}&file_type=${fileType}`;
    return this.http.get<SignedUrlResponse[]>(`${environment.apiUrl}/${endpoint}${params}`);
  }
}
