import { Attributes } from './attributes';
import { ControlOption } from './control-option';
import { TheiaControl } from './theia-control';
import { Validation } from './validation';

export type Control = InputControl | SelectControl | MultiSelectControl | ToggleControl | KeyValueControl | TextAreaControl | CompositeModuleControl | CodeControl | UploadControl | CompositeGroupControl

export interface CommonControl {
  type: TheiaControl;
  id: string;
  cli_arg: string;
  label: string;
  help?: string;
  readonly?: boolean;
  readonly_edit?: boolean;
  hidden?: boolean;
  new_hide?: boolean;
  datasource?: string;
}

export interface InputControl extends CommonControl {
  type: TheiaControl.Input;
  help?: string;
  default?: string | boolean | number;
  validations?: Validation[];
  attributes?: Attributes;
  inputType?: string;
  readonly?: boolean;
  readonly_edit?: boolean;
  hidden?: boolean;
  new_hide?: boolean;
  dependency?: ControlDependency;
  datasource?: string;
  env_not_required?: boolean;
  dynamic_datasource?: string;
}

export interface SelectControl extends CommonControl {
  type: TheiaControl.Select;
  help?: string;
  default?: string | boolean | number;
  validations?: Validation[];
  attributes?: Attributes;
  readonly?: boolean;
  readonly_edit?: boolean;
  hidden?: boolean;
  new_hide?: boolean;
  dependency?: ControlDependency;
  datasource?: string;
  env_not_required?: boolean;
  dynamic_datasource?: string;
  options?: ControlOption[];
}

export interface MultiSelectControl extends CommonControl {
  type: TheiaControl.MultiSelect;
  help?: string;
  default?: string | boolean | number;
  validations?: Validation[];
  attributes?: Attributes;
  readonly?: boolean;
  readonly_edit?: boolean;
  hidden?: boolean;
  new_hide?: boolean;
  dependency?: ControlDependency;
  datasource?: string;
  env_not_required?: boolean;
  dynamic_datasource?: string;
  options?: ControlOption[];
}

export interface ToggleControl extends CommonControl {
  type: TheiaControl.Toggle;
  help?: string;
  default?: boolean;
  validations?: Validation[];
  readonly?: boolean;
  readonly_edit?: boolean;
  hidden?: boolean;
  new_hide?: boolean;
  dependency?: ControlDependency;
  datasource?: string;
  env_not_required?: boolean;
  dynamic_datasource?: string;
}

export interface KeyValueControl extends CommonControl {
  type: TheiaControl.KeyValue;
  help?: string;
  default?: string;
  readonly?: boolean;
  readonly_edit?: boolean;
  hidden?: boolean;
  new_hide?: boolean;
  dependency?: ControlDependency;
  datasource?: string;
  env_not_required?: boolean;
  dynamic_datasource?: string;
  add_value_label?: string;
  value_label?: string;
  key_label?: string;
}

export interface TextAreaControl extends CommonControl {
  type: TheiaControl.TextArea;
  help?: string;
  default?: string;
  validations?: Validation[];
  attributes?: Attributes;
  inputType?: string;
  readonly?: boolean;
  readonly_edit?: boolean;
  hidden?: boolean;
  new_hide?: boolean;
  dependency?: ControlDependency;
  datasource?: string;
  env_not_required?: boolean;
  dynamic_datasource?: string;
}

export interface CompositeModuleControl extends CommonControl {
  type: TheiaControl.CompositeModule;
  default: undefined;
  modules: {
    type: TheiaControl.Toggle;
    id: string;
    cli_arg: string;
    label: string;
    default?: boolean;
    readonly?: boolean;
    readonly_edit?: boolean;
    controls: Control[];
  }[];
}

export interface CodeControl extends CommonControl {
  type: TheiaControl.Code;
  default: undefined;
  language?: string;
  datasource?: string;
  env_not_required?: boolean;
  dynamic_datasource?: string;
}

export interface UploadControl extends CommonControl {
  type: TheiaControl.Upload;
  default: undefined;
  multiple?: boolean;
  datasource?: string;
  env_not_required?: boolean;
  dynamic_datasource?: string;
  metadata?: Record<string, string>;
}

export interface CompositeGroupControl extends CommonControl {
  type: TheiaControl.CompositeGroup;
  default: undefined;
  controls: (CommonControl & (InputControl | SelectControl | MultiSelectControl | ToggleControl | CodeControl))[];
}

export interface ControlDependency extends CommonControl {
  controlId: string;
  field: string;
  datasourceDependency: boolean;
}

export function isInputControl(control: Control): control is CommonControl & InputControl {
  return control.type === TheiaControl.Input;
}

export function isKeyValueControl(control: Control): control is CommonControl & KeyValueControl {
  return control.type === TheiaControl.KeyValue;
}

export function isToggleControl(control: Control): control is CommonControl & ToggleControl {
  return control.type === TheiaControl.Toggle;
}

export function isSelectControl(control: Control): control is CommonControl & SelectControl {
  return control.type === TheiaControl.Select;
}

export function isMultiSelectControl(control: Control): control is CommonControl & MultiSelectControl {
  return control.type === TheiaControl.MultiSelect;
}

export function isTextAreaControl(control: Control): control is CommonControl & TextAreaControl {
  return control.type === TheiaControl.TextArea;
}

export function isCodeControl(control: Control): control is CommonControl & CodeControl {
  return control.type === TheiaControl.Code;
}

export function isUploadControl(control: Control): control is CommonControl & UploadControl {
  return control.type === TheiaControl.Upload;
}

export function isCompositeModuleControl(control: Control): control is CommonControl & CompositeModuleControl {
  return control.type === TheiaControl.CompositeModule;
}

export function isCompositeGroupControl(control: Control): control is CommonControl & CompositeGroupControl{
  return control.type === TheiaControl.CompositeGroup;
}

export function isComplexControl(control: Control): control is CommonControl & (KeyValueControl | CompositeModuleControl | CodeControl | UploadControl | CompositeGroupControl) {
  return control.type === TheiaControl.KeyValue || control.type === TheiaControl.CompositeModule || control.type === TheiaControl.Code || control.type === TheiaControl.Upload || control.type === TheiaControl.CompositeGroup;
}

export function hasDatasource(control: Control): control is Exclude<Control, CompositeGroupControl | CompositeModuleControl> {
  return control && (control.type !== TheiaControl.CompositeModule && control.type !== TheiaControl.CompositeGroup);
}

export function isSelectOrMultiSelect(control: Control): control is CommonControl & (SelectControl | MultiSelectControl) {
  return control && (control.type === TheiaControl.Select || control.type === TheiaControl.MultiSelect);
}
