import { Directive, ElementRef, HostListener, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';

@Directive({
  selector: '[theiaDropdown]'
})
export class DropdownDirective {

  visible = false;

  constructor(private eRef: ElementRef, @Inject(DOCUMENT) private document: Document) {
  }

  @HostListener('click', ['$event'])
  onClick(event: Event): void {
    event.preventDefault();
    this.visible = !this.visible;
    this.updateDropdown();
  }

  @HostListener('document:click', ['$event'])
  clickOut(event: Event): void {
    const link = this.eRef.nativeElement;
    if (!link.contains(event.target)) {
      this.visible = false;
      this.updateDropdown();
    }
  }

  updateDropdown(): void {
    const dropdown = this.eRef.nativeElement.nextSibling;
    if (this.visible) {
      dropdown.classList.add('show');
      const rect = dropdown.getBoundingClientRect();
      const width = this.document.documentElement.clientWidth;
      if (rect.right > width) {
        dropdown.style.left = `${width - rect.right - 16}px`;
      }
    } else {
      dropdown.classList.remove('show');
    }
  }
}
