import { ChangeDetectorRef, Component, ComponentFactoryResolver, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Action, Section, TheiaType } from '../../models/template';
import { EnvironmentService } from '../../services/environment.service';
import { InfoService } from '../../services/info.service';
import { TemplateService } from '../../services/template.service';
import { StaticActionDirective } from '../../directives/static-action.directive';
import { DynamicComponent } from '../../models/dynamic-component';
import { BehaviorSubject } from 'rxjs';
import { Environment } from '../../models/environment';

@Component({
  selector: 'theia-dynamic-action',
  templateUrl: './dynamic-action.component.html',
  styleUrls: ['./dynamic-action.component.scss']
})
export class DynamicActionComponent implements OnInit {

  section: Section | undefined;
  currentAction: Action | undefined;
  currentStepIndex: number = 0;
  @ViewChild(StaticActionDirective, {static: true}) staticHost!: StaticActionDirective;
  currentEnv$: BehaviorSubject<Environment | undefined>;

  TheiaType = TheiaType;

  constructor(private templateService: TemplateService,
              private activatedRoute: ActivatedRoute,
              private environmentService: EnvironmentService,
              private cdr: ChangeDetectorRef,
              private info: InfoService,
              private componentFactoryResolver: ComponentFactoryResolver) {

    this.currentEnv$ = environmentService.getCurrentEnvironment();
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe((params) => {
      const route = params.get('route') || '';
      const action = params.get('action') || '';
      this.section = this.templateService.getSectionByRoute(route);
      this.currentAction = this.templateService
        .getActionById(route, action);
      if (this.currentAction && this.currentAction.dynamic
        && this.currentAction.component && (this.currentEnv$.value || this.currentAction.env_not_required)) {
        this.loadDynamicComponent(this.currentAction.component);
      } else {
        this.staticHost?.viewContainerRef?.clear();
      }
      this.info.data = this.currentAction?.info || "";
    });

    this.environmentService.getCurrentEnvironment()
      .subscribe(() => {
        const action = this.currentAction;
        this.currentAction = undefined;
        this.cdr.detectChanges();
        this.currentAction = action;
      });
  }

  loadDynamicComponent(component: {file: string, className: string, data: unknown}) {
    const file = component.file;
    const className = component.className;
    import(`../${file}/${file}.component.ts`)
      .then(module => {
        setTimeout(() => {
          const componentFactory = this.componentFactoryResolver.resolveComponentFactory<DynamicComponent>(module[className]);
          const viewContainerRef = this.staticHost.viewContainerRef;
          viewContainerRef.clear();
          const componentRef = viewContainerRef.createComponent<DynamicComponent>(componentFactory);
          componentRef.instance.data = component.data;
        }, 0)
      });
  }
}
