import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter, HostBinding,
  HostListener,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { FormArray, UntypedFormArray, UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { switchMap, switchMapTo, tap } from 'rxjs/operators';
import { CommandResponse } from '../../models/command-response';
import { Control, OptionValue, Step, TheiaControl, TheiaStep, TheiaType } from '../../models/template';
import { Command } from '../../models/template/command';
import { CommandService } from '../../services/command.service';
import { EnvironmentService } from '../../services/environment.service';
import { InfoService } from '../../services/info.service';
import { LogService } from '../../services/log.service';
import { GridComponent, GridEvent } from '../grid/grid.component';
import { PaginationResponse } from '../../models/pagination-response';
import { DataSourceResponse } from '../../models/data-source-response';
import { DataSourceService } from '../../services/data-source.service';
import { SortOrder } from '../../models/grid';
import { TemplateService } from '../../services/template.service';

@Component({
  selector: 'theia-steps', templateUrl: './steps.component.html', styleUrls: ['./steps.component.scss'],
})
export class StepsComponent implements OnInit, OnChanges {

  @Input() steps: Step[] = [];
  @Input() actionId = '';
  @Input() sectionLabel? = '';
  @Input() actionLabel? = '';

  @Output() stepChanged: EventEmitter<number> = new EventEmitter<number>();

  @ViewChild(GridComponent) grid!: GridComponent;
  @ViewChild('resizableHost') resizableHost!: ElementRef;

  @HostBinding('style.cursor')
  get cursor() {
    if (this.mouseDown) {
      return 'col-resize';
    }
    return '';
  }
  @HostListener('document:mouseup')
  onMouseUp() {
    this.mouseDown = false;
  }

  submitting = false;
  currentStepIndex = 0;

  TheiaStep = TheiaStep;
  formGroup: UntypedFormGroup;
  showLog = false;
  log = '';
  isNew$: BehaviorSubject<boolean>;
  loading = false;
  paginationData?: PaginationResponse;
  data: DataSourceResponse[] = [];
  filteredData: DataSourceResponse[] = [];
  filter = '';
  sortKey = '';
  sortOrder: SortOrder = SortOrder.Asc;
  currentPage: number = 1;
  selectedRowId: string = '';
  infoText$: BehaviorSubject<string>;
  infoEnabled$: BehaviorSubject<boolean>;
  mouseDown: boolean = false;
  resizerPosition?: number;
  columnWidths: number[] = [];
  headerHeight?: number;

  constructor(private fb: UntypedFormBuilder,
              private commandService: CommandService,
              private logService: LogService,
              private infoService: InfoService,
              private environmentService: EnvironmentService,
              private dataSourceService: DataSourceService,
              private cdr: ChangeDetectorRef,
              private templateService: TemplateService,
              ) {
    this.formGroup = fb.group({});
    this.isNew$ = commandService.isNew$;
    this.infoText$ = infoService.data$;
    this.infoEnabled$ = infoService.enabled$;
  }

  get isReadOnly(): boolean {
    return !!this.currentStep.readonly;
  }

  get currentStep(): Step {
    return this.steps[this.currentStepIndex];
  }

  get firstStep(): Step {
    return this.steps[0];
  }


  get canCreate(): boolean {
    return !(this.firstStep?.single_value && this.data.length > 0) && !this.firstStep?.hide_add && !this.firstStep?.readonly;
  }

  ngOnInit() {
    this.formGroup.reset();
    this.sortKey = '';
    this.sortOrder = SortOrder.Asc;
    this.loadData();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.formGroup = this.fb.group({});
    this.currentStepIndex = 0;
    this.showLog = false;
    this.log = '';
    this.commandService.isNew = false;
    this.resizerPosition = undefined;
    this.columnWidths = [];
    this.loadData();
  }

  handleConfirm(confirmed: boolean, command: Command): void {
    command.show_confirmation = false;
    if (confirmed) {
      this.sendPayload(command);
    }
  }

  handleSubmit(command: Command): void {
    this.markFormGroupTouched(this.formGroup);
    if (this.formGroup.invalid) {
      return;
    }
    if (command.require_confirmation) {
      command.show_confirmation = true;
      return;
    }
    this.sendPayload(command);
  }

  sendPayload(command: Command): void {
    this.submitting = true;
    const payload = {
      ...command.command, ...this.removeExtraValues(this.formatValues(this.formGroup.value)),
    };

    this.commandService.runCommand(payload)
      .pipe(switchMap((resp: CommandResponse) => this.logService.getLog(resp.log_file)))
      .subscribe((log) => {
        this.log = log;
        this.submitting = false;
        this.showLog = true;
        this.commandService.isNew = false;

        if (this.actionId === 'Environments') {
          this.environmentService
            .getEnvironments()
            .subscribe();
        }

        if (this.grid) {
          this.loadData();
        }

      }, () => {
        this.submitting = false;
      });
  }

  next(): void {
    if (this.currentStepIndex === 0) {
      this.currentStepIndex++;
    }
    if (this.currentStep.readonly && !this.commandService.isNew) {
      this.formGroup.disable();
    } else {
      this.formGroup.enable();
    }
    this.markFormGroupTouched(this.formGroup);
    if (this.formGroup.invalid) {
      return;
    }
    this.stepChanged.emit(this.currentStepIndex);
  }

  back(): void {
    this.currentStepIndex--;
    if (this.currentStep.readonly && !this.commandService.isNew) {
      this.formGroup.disable();
    } else {
      this.formGroup.enable();
    }
    this.stepChanged.emit(this.currentStepIndex);
  }

  modalClose(): void {
    this.showLog = false;
  }

  selected(event: GridEvent): void {
    if (this.selectedRowId === '' && event.data?.id === this.selectedRowId) {
      return;
    }

    this.commandService.isNew = event.isNew;

    if (this.firstStep?.dynamic_template) {
      const url = this.parseDynamicUrl(event.isNew ? this.firstStep.default_template! : this.firstStep.dynamic_template, event.data);
      this.templateService.loadDynamicTemplate<Step>(url)
        .subscribe((step) => {
          this.steps[1] = step;
          this.rowSelected(event.data);
          this.next();
        });
      return;
    } else {
      this.rowSelected(event.data);
      this.next();
    }
  }

  reload() {
    this.currentStepIndex = 0;
    this.stepChanged.emit(this.currentStepIndex);
    this.rowSelected({});
    const params = this.firstStep?.pagination ? {
      start: (this.currentPage - 1) * (this.paginationData?.limit ?? 1) + 1, search: this.filter
    } : undefined;
    this.loadData(params);
  }

  loadData(params: Record<string, unknown> = {}): void {
    if (!this.firstStep) {
      return;
    }
    this.loading = true;
    const paginationData$: Observable<PaginationResponse> = this.firstStep?.pagination ? this.dataSourceService.getData<PaginationResponse>(this.firstStep.pagination, true, params) : of({
      size: 0, limit: 0
    });
    paginationData$
      .pipe(tap((data) => {
        this.paginationData = data;
      }), switchMapTo(this.dataSourceService.getData<DataSourceResponse[]>(this.firstStep.datasource, this.firstStep.env_param_required, params)))
      .subscribe((response) => {
        response.forEach((item, index) => item.id = index.toString());
        this.loading = false;
        this.data = response;
        if (this.firstStep?.pagination) {
          this.filteredData = this.data;
        } else {
          this.onFilter('');
        }
      });
  }

  onFilter(filter: string): void {
    this.filter = filter;
    if (this.firstStep?.pagination) {
      this.loadData({
        start: (this.currentPage - 1) * (this.paginationData?.limit ?? 1) + 1, search: this.filter
      });
      this.filteredData = this.data;
      this.sort();
    } else {
      this.filteredData = this.data.filter((item) => {
        return Object.values<string>(item)
          .some((value) => {
            return value?.toString().toLowerCase().includes(filter?.toString().toLowerCase());
          });
      });
      this.sort();
    }
  }

  sort(): void {
    if (!this.sortKey) {
      return;
    }
    this.filteredData
      .sort((a, b) => (a[this.sortKey] || '').toString().localeCompare((b[this.sortKey] || '').toString()));

    if (this.sortOrder === SortOrder.Desc) {
      this.filteredData.reverse();
    }
  }

  showInfo() {
    this.infoService.showInfoPanel(this.firstStep?.info || '');
  }

  create(): void {
    this.selected({
      isNew: true, data: {},
    });
  }

  rowSelected(row: DataSourceResponse = {}): void {
    if (this.firstStep?.readonly) {
      return;
    }

    this.selectedRowId = row.id;
    this.addControls(row);
    this.formGroup.reset();
    this.resetFormArrays();

    Object.entries(row).forEach(([key, value]) => {
      const control = this.formGroup.get(key);
      if (control) {
        try {
          if (Array.isArray(value) && value.every((item: unknown) => this.isObject(item))) {
            value.forEach((v: Record<string, any>) => {
              const values = Object
                .entries(v)
                .reduce((prev: Record<string, any>, [currKey, currValue]) => {
                  let newValue = currValue;
                  if (this.isObject(currValue) && currValue.type === TheiaType.DataOption) {
                    newValue = currValue.value;
                  }
                  prev[currKey] = newValue;
                  return prev;
                }, {});
              (control as FormArray).push(this.fb.group(values));
            });
          } else {
            control.setValue(value, {onlySelf: true});
          }
        } catch (e) {
          if (e instanceof TypeError && control instanceof UntypedFormArray) {
            control.setValue([]);
          }
        }
      }
    });


    this.cdr.detectChanges();
  }

  addControls(row: Record<string, any>): void {
    Object.keys(row)
      .forEach((key) => {
        if (!this.formGroup.get(key)) {
          if (Array.isArray(row[key]) && row[key].every((item: unknown) => this.isObject(item))) {
            this.formGroup.addControl(key, this.fb.array([]));
          } else {
            this.formGroup.addControl(key, this.fb.control(null));
          }
        }
      });
  }

  resetFormArrays() {
    Object.values(this.formGroup.controls).forEach(control => {
      if (control instanceof UntypedFormArray) {
        control.clear();
      }
    });
  }

  parseDynamicUrl(url: string, context: Record<string, string> | undefined = {}): string {
    const rx = /\${(.*?)}/g;
    return url.replace(rx, (_, key) => context[key]);
  }

  isObject(value: unknown): boolean {
    return typeof value === 'object' && !Array.isArray(value) && value !== null;
  }

  sortBy(column: string): void {
    if (this.sortKey === column) {
      this.sortOrder = this.sortOrder === SortOrder.Asc ? SortOrder.Desc : SortOrder.Asc;
    } else {
      this.sortOrder = SortOrder.Asc;
      this.sortKey = column;
    }
    this.sort();
  }

  onPageChange(page: number) {
    this.currentPage = page;
    this.loadData({
      start: (this.currentPage - 1) * (this.paginationData?.limit ?? 1) + 1, search: this.filter
    });
  }

  private markFormGroupTouched(formGroup: UntypedFormGroup): void {
    formGroup.markAsTouched();
    Object.values(formGroup.controls).forEach(control => {
      control.markAsTouched();
      if ((control as UntypedFormGroup).controls) {
        this.markFormGroupTouched(control as UntypedFormGroup);
      }
    });
  }

  private formatValues(payload: Record<string, unknown>): Record<string, unknown> {
    const controlReducer = (prev: Record<string, unknown>, curr: Control) => {
      switch (curr.type) {
        case TheiaControl.Toggle:
          prev[curr.id] = Boolean(prev[curr.id]).toString();
          break;
        case TheiaControl.KeyValue:
          const value = (prev[curr.id] as { key: string; value: string; }[])
            .reduce((prev: Record<string, string>, curr) => {
              prev[curr.key] = curr.value;
              return prev;
            }, {});
          prev[curr.id] = JSON.stringify(value).toString().replace(/"/g, '\\"');
          break;
        case TheiaControl.CompositeModule:
          curr.modules?.forEach((module) => {
            prev[module.id] = Boolean(prev[module.id]).toString();
            module.controls.reduce(controlReducer, prev);
          });
          break;
      }
      return prev;
    };
    return this.currentStep.controls?.reduce(controlReducer, payload) || {};
  }

  private removeExtraValues(payload: Record<string, unknown>): Record<string, unknown> {
    const ids: string[] = [];
    const idArgMap: Record<string, string> = this.currentStep
      ?.controls
      ?.reduce((prev: Record<string, string>, curr: Control) => {
        const addControl = (control: Control) => {
          ids.push(control.id);
          prev[control.id] = control.cli_arg || control.id;
        };
        if (curr.type === TheiaControl.CompositeModule) {
          curr.modules?.forEach((module) => {
            addControl(module as Control);
            module.controls.forEach(addControl);
          });
        } else {
          addControl(curr);
        }
        return prev;
      }, {}) || {};

    const result: { [key: string]: unknown } = {};
    Object.entries(payload).forEach(([key, value]) => {
      const validKey = ids.includes(key);
      if (validKey && value) {
        const arg = idArgMap[key];
        const control = this.findControlByKey(key);
        if (control && control.type === TheiaControl.CompositeGroup) {
          result[arg] = value;
          return;
        } else if (Array.isArray(value)) {
          value = value.map((val: OptionValue) => {
            if (val.type === TheiaType.DataOption) {
              return val.value;
            }
            return val;
          });
        } else if ((value as OptionValue).type === TheiaType.DataOption) {
          value = (value as OptionValue).value;
        }
        result[arg] = Array.isArray(value) ? value.toString() : value;
      }
    });
    return result;
  }

  private findControlByKey(id: string): Control | undefined {
    return this.currentStep.controls?.find((control) => control.id === id);
  }

  resizerMouseDown(event: MouseEvent) {
    if (event.button !== 0) {
      return;
    }
    this.mouseDown = true;
  }

  resizerMouseMove(event: MouseEvent) {
    if (this.mouseDown) {
      event.preventDefault();
      event.stopPropagation();
      const left = this.resizableHost.nativeElement.getBoundingClientRect().left;
      const percent = ((event.clientX - left) * 100) / this.resizableHost.nativeElement.getBoundingClientRect().width;
      this.resizerPosition = Math.min(Math.max(percent, 10), 90);
      this.columnWidths = [this.resizerPosition, 100 - this.resizerPosition];
    }
  }

  onHeaderResize(event: DOMRectReadOnly) {
    this.headerHeight = event.height;
  }
}
