import { Component, HostBinding, HostListener, Inject, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { Section, Template, TheiaType } from '../../models/template';
import { EnvironmentService } from '../../services/environment.service';
import { TemplateService } from '../../services/template.service';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'theia-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss'],
})
export class SidebarComponent implements OnInit {

  template$: BehaviorSubject<Template | undefined | null>;
  filteredSections: Section[] = [];
  TheiaType = TheiaType;
  currentSection: string | null = null;
  form: UntypedFormGroup = this.fb.group({
    filter: '',
  });

  @HostBinding('class.collapsed') collapsed = false;
  @HostBinding('class.animating') animating = false;

  @HostBinding('class.hover') hover = false;

  get filter(): UntypedFormControl {
    return this.form.get('filter') as UntypedFormControl;
  }

  get sections(): Section[] | undefined {
    return this.template$.getValue()?.sections?.filter((section) => section.id !== 'setup');
  }

  constructor(private templateService: TemplateService,
              private router: Router,
              private activatedRoute: ActivatedRoute,
              private fb: UntypedFormBuilder,
              private env: EnvironmentService,
              @Inject(DOCUMENT) private document: Document) {
    this.template$ = templateService.template$;
  }

  @HostListener('mousemove', ['$event'])
  onMouseMove(event: MouseEvent): void {
    event.stopPropagation();
    const filter = this.document.querySelector('.filter-sidebar');
    if (!this.collapsed || (filter && filter.contains((event.target as Node)))) {
      return;
    }
    this.hover = true;
  }

  @HostListener('mouseleave')
  onMouseLeave(): void {
    this.hover = false
  }

  ngOnInit(): void {
    this.activatedRoute
      .paramMap
      .subscribe((params) => {
        this.currentSection = params.get('route');
        if (this.currentSection) {
          const section = this.templateService.getSectionByRoute(this.currentSection);
          if (section) {
            section.open = true;
          }
        }
      });
    this.filter.valueChanges
      .subscribe((value) => {
        const sections = JSON.parse(JSON.stringify(this.sections));
        this.filteredSections = !value.length ? sections : sections
          .filter((section: Section) => {
            const re = new RegExp(value, 'i');
            const filteredActions = section?.actions?.filter(action => re.test(action.label)) || [];
            if (re.test(section.label) && !filteredActions.length) {
              return true;
            } else if (filteredActions.length) {
              section.open = true;
              section.actions = filteredActions;
              return true;
            }
            return false;
          }) || [];
      });
    this.updateFilteredSections();
    this.env.highlightedActions$.subscribe(this.updateFilteredSections.bind(this));

  }

  updateFilteredSections() {
    this.filteredSections = this.sections?.map((section) => {
      if ((section.actions || []).map(v => v.id).some(this.isHighlighted.bind(this))) {
        section.open = true;
      }
      return section;
    }) || [];
  }

  toggleSection(event: MouseEvent, section: Section): void {
    event.preventDefault();
    section.open = !section.open;
  }

  toggleSidebar(event: MouseEvent): void {
    event.preventDefault();
    this.animating = true;
    this.collapsed = !this.collapsed;
    setTimeout(() => this.animating = false, 250);
  }

  isHighlighted(id: string): boolean {
    return this.env.highlightedActions$.value.includes(id);
  }

  collapseAll(event: MouseEvent) {
    event.preventDefault();
    this.filteredSections.forEach((section: Section) => {
      section.open = false;
    });
  }

  hasSectionsOpen() {
    return this.filteredSections.some(section => section.open);
  }
}
