import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'theia-pagination', templateUrl: './pagination.component.html', styleUrls: ['./pagination.component.scss']
})
export class PaginationComponent implements OnInit {
  @Input() totalItems = 0;
  @Input() currentPage = 1;
  @Input() itemsPerPage = 50;

  @Output() pageChange = new EventEmitter<number>();
  @Output() itemsPerPageChange = new EventEmitter<number>();

  pages: (number | string)[] = [];
  maxPages = 5;

  ngOnInit(): void {
    this.setPages();
  }

  handlePageClick(event: MouseEvent, page: number) {
    event.preventDefault();
    this.pageChange.emit(page);
    this.currentPage = page;
    this.setPages();
  }

  setPages() {
    const range = (lo: number, hi: number) => Array.from({length: hi - lo}, (_, i) => i + lo);

    const pagination = (count: number, page: number, total: number) => {
      const start = Math.max(1, Math.min(page - Math.floor((count - 3) / 2), total - count + 2));
      const end = Math.min(total, Math.max(page + Math.floor((count - 2) / 2), count - 1));
      return [
        ...(start > 2 ? [1, "..."] : start > 1 ? [1] : []),
        ...range(start, end + 1)
        , ...(end < total - 1 ? ["...", total] : end < total ? [total] : [])
      ];
    };
    this.pages = pagination(this.maxPages, this.currentPage, Math.ceil(this.totalItems/this.itemsPerPage));
  }


  protected readonly Math = Math;
}
