import { Directive, ElementRef, HostListener, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';

@Directive({
  selector: '[theiaSubmenu]'
})
export class SubmenuDirective {

  visible = false;

  constructor(private eRef: ElementRef, @Inject(DOCUMENT) private document: Document) { }

  @HostListener('mouseenter', ['$event'])
  onMouseEnter(event: Event): void {
    event.preventDefault();
    this.visible = true;
    this.updateSubmenu();
  }

  @HostListener('mouseleave', ['$event'])
  onMouseLeave(event: Event): void {
    event.preventDefault();
    this.visible = false;
    this.updateSubmenu();
  }

  updateSubmenu() {
    const dropdown = this.eRef.nativeElement.querySelector('.submenu');
    const rect = this.eRef.nativeElement.getBoundingClientRect();
    if (this.visible) {
      Object.assign(dropdown.style, {
        left: `${rect.x + rect.width}px`,
        top: `${rect.y - 9}px`
      });
      dropdown.classList.add('show');
    } else {
      dropdown.classList.remove('show');
    }
  }

}
