import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { Control, Step, TheiaControl } from '../../models/template';
import { CommandService } from '../../services/command.service';
import { DataSourceService } from '../../services/data-source.service';
import { InfoService } from '../../services/info.service';
import { ICustomWindow, WindowRefService } from '../../services/window-ref.service';
import { BehaviorSubject } from 'rxjs';
import {
  CompositeGroupControl,
  isCompositeGroupControl,
  isCompositeModuleControl,
  isInputControl
} from '../../models/template/control';
import { DynamicFormService, FormOptions } from '../../services/dynamic-form.service';

@Component({
  selector: 'theia-dynamic-form',
  templateUrl: './dynamic-form.component.html',
  styleUrls: ['./dynamic-form.component.scss'],
})
export class DynamicFormComponent implements OnInit {

  @Input() step: Step | undefined;
  @Input() formGroup: UntypedFormGroup;
  @Input() selectedId: string = "";
  TheiaControl = TheiaControl;
  isNew$: BehaviorSubject<boolean>;
  protected readonly isCompositeModuleControl = isCompositeModuleControl;
  protected readonly isCompositeGroupControl = isCompositeGroupControl;
  protected readonly isInputControl = isInputControl;
  private window: ICustomWindow;

  constructor(private fb: UntypedFormBuilder, private commandService: CommandService, private windowRef: WindowRefService, private infoService: InfoService, private dataSourceService: DataSourceService, private formService: DynamicFormService, private cdr: ChangeDetectorRef) {
    this.formGroup = fb.group({});
    this.window = windowRef.nativeWindow;
    this.isNew$ = commandService.isNew$;
  }

  get controls() {
    if (!this.step) {
      return undefined;
    }
    return this.step.controls;
  }

  ngOnInit(): void {
    const options: FormOptions = {
      datasource: this.step?.datasource,
      env_param_required: this.step?.env_param_required,
      readonly: this.step?.readonly
    };
    this.formService.initialize(this.formGroup, this.step?.controls || [], this.cdr, options);
  }

  showInfo() {
    this.infoService.showInfoPanel(this.step?.info || '');
  }

  toCompositeControl(control: Control): CompositeGroupControl {
    return control as CompositeGroupControl;
  }
}
