import { Injectable } from '@angular/core';
import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Observable } from 'rxjs';
import { CloudService } from '../services/cloud.service';
import { AllowedAppsService } from '../services/allowed-apps.service';

@Injectable()
export class CloudInterceptor implements HttpInterceptor {

  constructor(private cloud: CloudService, private app: AllowedAppsService) {}

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    const params = request.params
      .set('cloud', this.cloud.getSelectedCloud().value)
      .set('rc_app_context', this.app.getSelectedApp().value);
    return next.handle(request.clone({
      params,
    }));
  }
}
