import { Component, OnInit } from '@angular/core';
import { AuthService } from '@auth0/auth0-angular';
import { TemplateService } from '../../services/template.service';
import { TheiaType } from '../../models/template';


@Component({
  selector: 'theia-auth-error',
  templateUrl: './auth-error.component.html',
  styleUrls: ['./auth-error.component.scss']
})
export class AuthErrorComponent implements OnInit {
  error: {error: string; description: string; header: string};

  constructor(private authService: AuthService, private templateService: TemplateService) {
    this.error = {
      header: 'Unauthorized',
      error: '',
      description: ''
    }
  }

  ngOnInit(): void {
    this.templateService.template$.next({
      title: '',
      type: TheiaType.Template,
      version: '',
      sections:[],
      footer: {
        "columns": [
        ],
      }
    });
    this.authService.error$
      .subscribe(error => {
        if (error.message === 'email_not_verified') {
          this.error = {
            header: 'Unauthorized',
            error: 'Email address not verified',
            description: 'Please check your email and verify your account'
          };
        }
      });
  }

}
