import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { LocalStorageService } from './local-storage.service';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { tap } from 'rxjs/operators';
import { CloudsResponse } from '../models/clouds-response';

@Injectable({
  providedIn: 'root'
})
export class CloudService {

  availableClouds$: BehaviorSubject<CloudsResponse> = new BehaviorSubject<CloudsResponse>({
    aws: {
      cloud_label: "AWS",
      account_label: "Account",
      region_label: "Region",
      vpc_label: "VPC"
    }
  });
  selectedCloud$: BehaviorSubject<string> = new BehaviorSubject<string>("");

  constructor(private http: HttpClient, private localStorageService: LocalStorageService) { }

  getClouds(): Observable<CloudsResponse> {
    return this.http
      .get<CloudsResponse>(`${environment.apiUrl}/clouds`)
      .pipe(
        tap((response) => {
          this.availableClouds$.next(response);
        })
      );
  }

  getSelectedCloud(): BehaviorSubject<string> {
    if (!this.selectedCloud$.getValue()) {
      const selectedCloud = this.localStorageService.getItem('selectedCloud');
      if (selectedCloud) {
        this.selectedCloud$.next(selectedCloud);
      } else {
        this.setSelectedCloud('aws');
      }
    }
    return this.selectedCloud$;
  }

  setSelectedCloud(value: string) {
    this.selectedCloud$.next(value);
    this.localStorageService.setItem('selectedCloud', value);
  }
}
