import {inject} from '@angular/core';
import {ActivatedRouteSnapshot, ResolveFn, Router, RouterStateSnapshot} from '@angular/router';
import {of} from 'rxjs';
import {catchError, switchMapTo, tap} from 'rxjs/operators';
import {Template} from '../models/template';
import {TemplateService} from './template.service';
import {AccountStatus} from '../models/account-status';
import {DOCUMENT} from '@angular/common';

export const templateResolver: ResolveFn<Template | null> =
  (route: ActivatedRouteSnapshot, state: RouterStateSnapshot) => {
    const templateService = inject(TemplateService);
    const document = inject(DOCUMENT);
    const router = inject(Router);

    if (templateService.template$.value) {
      return templateService.template$.value;
    }

    return templateService.getVerifyStatus()
      .pipe(tap(data => {
        validateStatus(data, state, document, router);
      }), switchMapTo(templateService.getTemplate()), catchError(() => of(null)));
  };

const validateStatus = (status: AccountStatus | undefined, state: RouterStateSnapshot, document: Document, router: Router) => {

  if (status?.redirect && state.url !== status.redirect) {
    if (isExternalURL(status.redirect)) {
      document.location.href = status.redirect;
    } else {
      router.navigateByUrl(status.redirect);
    }
  }
}

const isExternalURL = (url: string): boolean => {
  return /^https?:\/{2}\S+$/.test(url);
}
