import { Component, Input, OnInit } from '@angular/core';
import {
  AbstractControl,
  UntypedFormArray,
  UntypedFormBuilder,
  UntypedFormGroup,
  ValidationErrors,
  ValidatorFn,
  Validators
} from '@angular/forms';

@Component({
  selector: 'theia-key-value-editor',
  templateUrl: './key-value-editor.component.html',
  styleUrls: ['./key-value-editor.component.scss'],
})
export class KeyValueEditorComponent implements OnInit {

  isDisabled = false;

  @Input() parent: UntypedFormGroup = new UntypedFormGroup({});
  @Input() id: string = '';
  @Input() readonly: boolean = false;
  @Input() addLabel?: string = 'Add value';
  @Input() keyLabel?: string = 'Key';
  @Input() valueLabel?: string = 'Value';
  @Input() loading?: boolean = false;

  @Input()
  set value(val: string) {
    if (val) {
      const parsed: Record<string, string> = JSON.parse(val);
      this.control.setValue([]);
      for (const [key, val] of Object.entries(parsed)) {
        this.addRow(key, val);
      }
    }
  }

  constructor(private fb: UntypedFormBuilder) {
  }

  get control(): UntypedFormArray {
    return this.parent.get(this.id) as UntypedFormArray;
  }

  get pairs(): UntypedFormGroup[] {
    return this.control.controls as UntypedFormGroup[];
  }

  ngOnInit(): void {
    this.parent.setControl(this.id, this.fb.array([]));
  }

  uniqueValidator(name: string): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      const formArray = control?.parent?.parent as UntypedFormArray;
      if (!formArray) {
        return null;
      }
      for (const group of formArray.controls) {
        const otherControl = group.get(name);
        if (otherControl?.value === control?.value && control !== otherControl) {
          return {unique: true};
        }
      }
      return null;
    };
  }

  addRow(key: string = '', value: string = '') {
    this.control.push(this.fb.group({
      key: [key, [Validators.required, this.uniqueValidator('key')]],
      value: [value, Validators.required],
    }));
  }

  deleteRow(index: number) {
    this.control.removeAt(index);
    for (const control of this.control.controls) {
      // check for unique keys
      control.get('key')?.updateValueAndValidity();
    }
  }

  isInvalid(group: UntypedFormGroup, name: string): boolean {
    const control = group.get(name);
    return !!((control?.touched || control?.dirty) && control?.invalid);
  }

  errorMessage(group: UntypedFormGroup, name: string) {
    const control = group.get(name);
    let result = '';
    if (control?.hasError('required')) {
      result = 'This field is required.';
    } else if (control?.hasError('unique')) {
      result = 'Keys must be unique.';
    }
    return result;
  }
}
