import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { APP_INITIALIZER, NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { NgSelectModule } from '@ng-select/ng-select';
import { MarkdownModule } from 'ngx-markdown';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ActionsComponent } from './components/actions/actions.component';
import { ArchitectureWizardComponent } from './components/architecture-wizard/architecture-wizard.component';
import { ArchitectureComponent } from './components/architecture/architecture.component';
import { ConfirmModalComponent } from './components/confirm-modal/confirm-modal.component';
import { DiagramComponent } from './components/diagram/diagram.component';
import { DynamicActionComponent } from './components/dynamic-action/dynamic-action.component';
import { DynamicControlComponent } from './components/dynamic-control/dynamic-control.component';
import { DynamicFormComponent } from './components/dynamic-form/dynamic-form.component';
import { DynamicSectionComponent } from './components/dynamic-section/dynamic-section.component';
import { ErrorComponent } from './components/error/error.component';
import { FooterComponent } from './components/footer/footer.component';
import { GridComponent } from './components/grid/grid.component';
import { HeaderComponent } from './components/header/header.component';
import { HomeComponent } from './components/home/home.component';
import { ReadmeComponent } from './components/readme/readme.component';
import { LayoutComponent } from './components/layout/layout.component';
import { ModalComponent } from './components/modal/modal.component';
import { NotFoundComponent } from './components/not-found/not-found.component';
import { SidebarComponent } from './components/sidebar/sidebar.component';
import { StepsComponent } from './components/steps/steps.component';
import { TabBarComponent } from './components/tab-bar/tab-bar.component';
import { ControlAttributesDirective } from './directives/control-attributes.directive';
import { DropdownDirective } from './directives/dropdown.directive';
import { RemoveNullPipe } from './pipes/remove-null.pipe';
import { InfoComponent } from './components/info/info.component';
import { KeyValueEditorComponent } from './components/key-value-editor/key-value-editor.component';
import { AuthHttpInterceptor, AuthModule, AuthService } from '@auth0/auth0-angular';
import { environment } from '../environments/environment';
import { AuthErrorComponent } from './components/auth-error/auth-error.component';
import { SubmenuDirective } from './directives/submenu.directive';
import { AccordionModule } from './components/accordion/accordion.module';
import { CloudInterceptor } from './interceptors/cloud.interceptor';
import { APP_BASE_HREF, NgOptimizedImage } from '@angular/common';
import { StaticActionDirective } from './directives/static-action.directive';
import { FileUploadModule } from 'ng2-file-upload';
import { FileSizePipe } from './pipes/file-size.pipe';
import { DashboardModule } from './components/dashboard/dashboard.module';
import { TemplateReloadInterceptor } from './interceptors/template-reload.interceptor';
import { PaginationComponent } from './components/pagination/pagination.component';
import { TypeAheadComponent } from './components/type-ahead/type-ahead.component';
import { catchError, take, tap } from 'rxjs/operators';
import { Observable, of } from 'rxjs';
import { CompositeGroupComponent } from './components/composite-group/composite-group.component';
import { ResizeObserverDirective } from './directives/resize-observer.directive';
import { LogStreamDirective } from './directives/log-stream.directive';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    TabBarComponent,
    ActionsComponent,
    ArchitectureComponent,
    DynamicSectionComponent,
    DynamicControlComponent,
    ControlAttributesDirective,
    DynamicFormComponent,
    StepsComponent,
    GridComponent,
    DropdownDirective,
    ConfirmModalComponent,
    ModalComponent,
    FooterComponent,
    ErrorComponent,
    NotFoundComponent,
    LayoutComponent,
    RemoveNullPipe,
    ArchitectureWizardComponent,
    DiagramComponent,
    SidebarComponent,
    DynamicActionComponent,
    HomeComponent,
    ReadmeComponent,
    InfoComponent,
    KeyValueEditorComponent,
    AuthErrorComponent,
    SubmenuDirective,
    StaticActionDirective,
    FileSizePipe,
    PaginationComponent,
    TypeAheadComponent,
    CompositeGroupComponent,
    ResizeObserverDirective,
    LogStreamDirective,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    MarkdownModule.forRoot(),
    NgSelectModule,
    AuthModule.forRoot({
      ...environment.auth0,
      errorPath: '/auth-error',
      authorizationParams: {
        redirect_uri: window.location.origin + environment.redirectUri,
        ...environment.authorizationParams
      },
      httpInterceptor: {
        allowedList: [{
          uri: '*',
          allowAnonymous: true
        }]
      }
    }),
    AccordionModule,
    FileUploadModule,
    DashboardModule,
    NgOptimizedImage
  ],
  providers: [
    {provide: APP_INITIALIZER, useFactory: initializeAppFactory, deps: [AuthService], multi:true},
    {provide: HTTP_INTERCEPTORS, useClass: AuthHttpInterceptor, multi: true},
    {provide: HTTP_INTERCEPTORS, useClass: CloudInterceptor, multi: true},
    {provide: HTTP_INTERCEPTORS, useClass: TemplateReloadInterceptor, multi: true},
    {provide: APP_BASE_HREF, useValue: environment.baseHref}
  ],
  bootstrap: [AppComponent],
})
export class AppModule {
}

function initializeAppFactory(auth: AuthService): () => Observable<any> {
  return () => auth.user$
    .pipe(
      take(1),
      tap(user => {
        const appMetadata = user!["https://rapid-cloud.io/app_metadata"];
        if (appMetadata?.host_url) {
          environment.apiUrl = appMetadata.host_url;
        }
      }),
      catchError((err) => {
        return of(err);
      })
    );
}
