import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, forkJoin, Observable, of } from 'rxjs';
import { map, mergeMap, tap } from 'rxjs/operators';
import { environment } from '../../environments/environment';
import { Action, Section, Template, TheiaType } from '../models/template';
import { EnvironmentService } from './environment.service';
import { AccountStatus } from '../models/account-status';

@Injectable({
  providedIn: 'root'
})
export class TemplateService {
  template$: BehaviorSubject<Template | undefined | null> = new BehaviorSubject<Template | undefined | null>(undefined);
  accountStatus$: BehaviorSubject<AccountStatus | undefined> = new BehaviorSubject<AccountStatus | undefined>(undefined);

  constructor(private http: HttpClient,
              private environmentService: EnvironmentService) {
  }

  getTemplate(): Observable<Template | null> {

    return this.http.get<Template>(`${environment.apiUrl}/template`)
      .pipe(
        mergeMap((data) => this.loadObjectRefs(data)),
        tap<Template | null>(data => this.template$.next(data)),
      );
  }

  getVerifyStatus() {
    return this.http.get<AccountStatus>(`${environment.apiUrl}/verify_status`)
      .pipe(
        tap<AccountStatus>(data => this.accountStatus$.next(data)),
      );
  }

  getDiagramData<DiagramType>(endpoint: string, defaultValue: DiagramType): Observable<DiagramType> {
    if (!endpoint) {
      return of(defaultValue);
    }
    const env = this.environmentService.getCurrentEnvironment().value;
    if (!env) {
      return of(defaultValue);
    }
    return this.http
      .get<DiagramType>(`${environment.apiUrl}/${endpoint}?env=${env.name}`);
  }

  getSectionByRoute(route: string): Section | undefined {
    const template = this.template$.getValue();
    return template?.sections
      .find(section => section.type === TheiaType.Section && section.route === route);
  }

  getFirstRoute(): string {
    const template = this.template$.getValue();
    const first = template?.sections[0];
    return first?.type === TheiaType.Section ? first.route : '';
  }

  getActionById(route: string, id: string): Action | undefined {
    const section = this.getSectionByRoute(route);
    return section?.actions?.find((action: Action) => action.id === id);
  }


  loadObjectRefs(data: any): Observable<any> {
    const observables: Record<string, Observable<unknown>> = {};

    for (const key of Object.keys(data)) {
      if (key === '$ref' && data[key]) {
        observables[key] = this.http.get(`${environment.apiUrl}/${data.$ref}`)
          .pipe(
            mergeMap((data) => this.loadObjectRefs(data))
          );
      } else if (Array.isArray(data[key]) && data[key].length) {
        observables[key] = this.loadObjectRefs(data[key])
          .pipe(
            map((res: any[]) => Object.entries(res).reduce((acc: any, [key, value]) => {
              acc[key] = value;
              return acc;
            }, []))
          );
      }
      else if (!Array.isArray(data[key]) && typeof data[key] === 'object' && data[key] !== null) {
        observables[key] = this.loadObjectRefs(data[key]);
      }
      else {
        observables[key] = of(data[key]);
      }
    }
    return forkJoin(observables)
      .pipe(
        mergeMap((res: any) => {
          const result = {
            ...data,
            ...res,
            ...res['$ref']
          };
          delete result['$ref'];
          return of(result);
        })
      );
  }

  loadDynamicTemplate<T>(url: string): Observable<T> {
    return this.http.get<T>(url);
  }
}
