import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Action, Section, TheiaStep } from '../../models/template';
import { EnvironmentService } from '../../services/environment.service';
import { TemplateService } from '../../services/template.service';

@Component({
  selector: 'theia-dynamic-section',
  templateUrl: './dynamic-section.component.html',
  styleUrls: ['./dynamic-section.component.scss'],
})
export class DynamicSectionComponent implements OnInit {

  actions: Action[] | undefined = [];
  currentAction: Action | undefined;
  TheiaStep = TheiaStep;
  collapsed = false;
  section: Section | undefined;

  constructor(
    private templateService: TemplateService,
    private activatedRoute: ActivatedRoute,
    private environmentService: EnvironmentService,
    private cdr: ChangeDetectorRef,
    private router: Router
  ) {
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe((params) => {
      const route = params.get('route') || '';
      this.section = this.templateService
        .getSectionByRoute(route);
      this.currentAction = undefined;
      if (!this.section) {
        const firstRoute = this.templateService
          .getFirstRoute();
        this.router
          .navigate(['/action', firstRoute]);
      } else {
        this.actions = this.section.actions;
      }
    });

    this.environmentService.getCurrentEnvironment()
      .subscribe(() => {
        const action = this.currentAction;
        this.currentAction = undefined;
        this.cdr.detectChanges();
        this.currentAction = action;
      });
  }

  handleActionSelected(action?: Action): void {
    this.currentAction = action;
  }

  toggleCollapse(): void {
    this.collapsed = !this.collapsed;
  }
}
