import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ArchitectureComponent } from './components/architecture/architecture.component';
import { DynamicActionComponent } from './components/dynamic-action/dynamic-action.component';
import { DynamicSectionComponent } from './components/dynamic-section/dynamic-section.component';
import { HomeComponent } from './components/home/home.component';
import { ReadmeComponent } from './components/readme/readme.component';
import { NotFoundComponent } from './components/not-found/not-found.component';
import {environmentResolver} from './services/environment.resolver';
import {templateResolver} from './services/template.resolver';
import { AuthGuard } from '@auth0/auth0-angular';
import { AuthErrorComponent } from './components/auth-error/auth-error.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full',
  },
  {
    path: 'action',
    redirectTo: 'action/',
    pathMatch: 'full',
  },
  {
    path: 'architecture',
    component: ArchitectureComponent,
    canActivate: [AuthGuard],
    resolve: {
      template: templateResolver,
      env: environmentResolver,
    },
  },
  {
    path: 'home',
    component: HomeComponent,
    canActivate: [AuthGuard],
    resolve: {
      template: templateResolver,
      env: environmentResolver,
    },
  },
  {
    path: 'readme',
    component: ReadmeComponent,
    canActivate: [AuthGuard],
    resolve: {
      template: templateResolver,
      env: environmentResolver,
    },
  },
  {
    path: 'auth-error',
    component: AuthErrorComponent,
  },
  {
    path: 'action/:route',
    component: DynamicSectionComponent,
    canActivate: [AuthGuard],
    resolve: {
      template: templateResolver,
      env: environmentResolver,
    },
  },
  {
    path: 'action/:route/:action',
    component: DynamicActionComponent,
    canActivate: [AuthGuard],
    resolve: {
      template: templateResolver,
      env: environmentResolver,
    },
  },
  {
    path: '**',
    component: NotFoundComponent,
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
