import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { CommandResponse } from '../models/command-response';
import { EnvironmentService } from './environment.service';

@Injectable({
  providedIn: 'root'
})
export class CommandService {

  isNew$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  constructor(private http: HttpClient, private env: EnvironmentService) {
  }

  get isNew(): boolean {
    return this.isNew$.value;
  }

  set isNew(value: boolean) {
    this.isNew$.next(value);
  }

  runCommand(payload: { [key: string]: string | unknown }): Observable<CommandResponse> {
    const env = this.env.getCurrentEnvironment().getValue();
    payload.env = env?.name;
    return this.http.post<CommandResponse>(`${environment.apiUrl}/command`, payload);
  }
}
