import { ChangeDetectorRef, Injectable } from '@angular/core';
import { Control, OptionValue, TheiaControl, TheiaType, Validation } from '../models/template';
import { hasDatasource, isComplexControl } from '../models/template/control';
import {
  FormArray,
  FormGroup,
  UntypedFormArray,
  UntypedFormBuilder,
  UntypedFormControl,
  UntypedFormGroup,
  ValidatorFn,
  Validators
} from '@angular/forms';
import { environment } from '../../environments/environment';
import { ICustomWindow, WindowRefService } from './window-ref.service';
import { debounceTime, distinctUntilChanged, filter } from 'rxjs/operators';
import { DataSourceResponse } from '../models/data-source-response';
import { CommandService } from './command.service';
import { DataSourceService } from './data-source.service';

export interface FormOptions {
  readonly?: boolean;
  datasource?: string;
  env_param_required?: boolean;
}

@Injectable({
  providedIn: 'root'
})
export class DynamicFormService {
  private window: ICustomWindow;

  constructor(private fb: UntypedFormBuilder,
              private windowRef: WindowRefService,
              private commandService: CommandService,
              private dataSourceService: DataSourceService) {
    this.window = windowRef.nativeWindow;
  }

  initialize(formGroup: UntypedFormGroup, controls: Control[], cdr: ChangeDetectorRef, options: FormOptions) {
    const dependencyControls: Control[] = [];
    const selectControls: Control[] = [];
    const dynamicDatasourceControls: Control[] = [];

    const addControls = (controls?: Control[]) => {
      controls
        ?.forEach((control) => {

          if (control.type === TheiaControl.CompositeModule) {
            control.modules?.forEach((module) => {
              this.addControl(module, formGroup, dependencyControls, selectControls, dynamicDatasourceControls);
              addControls(module.controls);
            });
            return;
          }
          this.addControl(control, formGroup, dependencyControls, selectControls, dynamicDatasourceControls);
        });
    };

    addControls(controls);
    this.initializeSelectControls(formGroup, selectControls, controls);
    this.initializeDependencyControls(formGroup, dependencyControls);
    this.initializeDynamicDatasourceControls(formGroup, dynamicDatasourceControls, controls);

    if (options.readonly && !this.commandService.isNew) {
      formGroup.disable();
    }
    if (options.datasource) {
      this.dataSourceService
        .getData<DataSourceResponse[]>(options.datasource, options.env_param_required)
        .subscribe(([response]) => {
          Object.keys(formGroup.controls)
            .forEach((key) => {
              const control = formGroup.controls[key];
              if (control instanceof FormArray) {
                formGroup.controls[key].setValue(response[key] || control.value);
              } else {
                formGroup.controls[key].setValue(response[key]);
              }
            });
        });
    }
  }

  private addControl(control: Control,
                     formGroup: FormGroup,
                     dependencyControls: Control[] = [],
                     selectControls: Control[] = [],
                     dynamicDatasourceControls: Control[] = []) {
    const formControl = formGroup.get(control.id);
    if (formControl) {
      formControl.setValidators(this.getValidatorList(control));
      formControl.updateValueAndValidity();
    } else {
      if (control.type === TheiaControl.CompositeGroup) {
        formGroup.addControl(control.id, new UntypedFormArray([]));
      } else {
        formGroup.addControl(control.id, this.fb.control(control.default, this.getValidatorList(control)));
      }
    }
    if (!isComplexControl(control) && control.dependency?.controlId) {
      dependencyControls.push(control);
    }
    if (control.type === TheiaControl.Select) {
      selectControls.push(control);
    }
    if (hasDatasource(control) && control.dynamic_datasource) {
      dynamicDatasourceControls.push(control);
    }
  }

  private getValidatorList(control: Control): ValidatorFn[] {
    let result: ValidatorFn[] = [];

    if (!isComplexControl(control)) {
      if (control.validations) {
        result = control.validations
          .reduce<ValidatorFn[]>((prev: ValidatorFn[], curr: Validation): ValidatorFn[] => {
            const validationTypeMap: { [key: string]: ValidatorFn } = {
              'Theia::Validation::Email': Validators.email,
              'Theia::Validation::Max': Validators.max(Number(curr.value)),
              'Theia::Validation::Min': Validators.min(Number(curr.value)),
              'Theia::Validation::MinLength': Validators.minLength(Number(curr.value)),
              'Theia::Validation::MaxLength': Validators.maxLength(Number(curr.value)),
              'Theia::Validation::Required': Validators.required,
              'Theia::Validation::Pattern': Validators.pattern(String(curr.value)),
            };
            const validatorFn = validationTypeMap[curr.type];
            if (validatorFn) {
              prev.push(validatorFn);
            }
            return prev;
          }, []);
      }
    }

    return result || [];
  }

  private initializeSelectControls(formGroup: FormGroup, selectControls: Control[], controls: Control[]) {
    selectControls.forEach(control => {
      if (control.type === TheiaControl.Select || control.type === TheiaControl.MultiSelect) {
        const formControl = formGroup.get(control.id);
        const disableOption = (option: string | OptionValue) => {
          if (!option) {
            return;
          }
          const optionValue = option as OptionValue;
          let selectedOption: OptionValue;
          if (optionValue.type && optionValue.type === TheiaType.DataOption) {
            selectedOption = optionValue;
          } else if (option) { // is string
            const selected = control.options?.find(opt => {
              if (typeof opt.value === 'string') {
                return opt.value === option;
              }
              return opt.value.value === option;
            });
            if (selected) {
              selectedOption = selected.value as OptionValue;
            } else {
              return;
            }
          } else {
            return;
          }
          (selectedOption.disableControls || []).forEach(id => {
            const control = formGroup.get(id);
            if (control) {
              setTimeout(() => {
                control.disable();
              }, 1);
            }
          });
          (selectedOption.enableControls || []).forEach(id => {
            const control = formGroup.get(id);
            if (control) {
              setTimeout(() => {
                control.enable();
              }, 1);
            }
          });
          (selectedOption.hideControls || []).forEach(id => {
            const control = this.findControl(id, controls);
            if (control) {
              setTimeout(() => {
                control.hidden = true;
                formGroup.get(id)?.disable();
              }, 1);
            }
          });
          (selectedOption.showControls || []).forEach(id => {
            const control = this.findControl(id, controls);
            if (control) {
              setTimeout(() => {
                control.hidden = false;
                formGroup.get(id)?.enable();
              }, 1);
            }
          });
        };
        const selected = formControl?.value;
        if (selected) {
          const selectedOption = control.options?.find(option => {
            if (typeof option.value === 'string') {
              return option.value === selected;
            }
            return option.value.value === selected;
          });
          if (selectedOption) {
            disableOption(selectedOption.value as OptionValue);
          }
        }
        if (formControl) {
          formControl.valueChanges
            .subscribe(disableOption);
        }
      }
    });
  }

  private initializeDependencyControls(formGroup: FormGroup, dependencyControls: Control[]) {
    dependencyControls.forEach(control => {
      const dependencyControl = this.getDependencyControl(formGroup, control);
      const formControl = formGroup.get(control.id);

      if (dependencyControl && (control.type === TheiaControl.Select || control.type === TheiaControl.MultiSelect)) {
        dependencyControl.valueChanges
          .subscribe((value: string | OptionValue) => {
            if ((value as OptionValue).data) {
              const field = control?.dependency?.field;
              const datasourceDependency = control?.dependency?.datasourceDependency;
              if (field) {
                const newValue = (<OptionValue>value).data[control?.dependency?.field as string];
                if (datasourceDependency) {
                  const newDatasource = new URL(`${environment.apiUrl}/${control.datasource}`, this.window.location.origin);
                  newDatasource.searchParams.set(field, newValue);
                  control.datasource = newDatasource.pathname.replace(`${environment.apiUrl}/`, '') + newDatasource.search;
                } else {
                  formControl?.setValue(newValue);
                }
              }
            }
          });
      }
    });
  }

  private getDependencyControl(formGroup: FormGroup, control: Control): UntypedFormControl | undefined {
    return !isComplexControl(control) && control.dependency?.controlId ? formGroup.get(control.dependency?.controlId as string) as UntypedFormControl : undefined;
  }

  private initializeDynamicDatasourceControls(formGroup: FormGroup, dynamicDatasourceControls: Control[], stepControls: Control[]): void {
    dynamicDatasourceControls.forEach(control => {
      if (hasDatasource(control)) {
        const rx = /\${(.*?)}/g;
        let result: RegExpExecArray | null;
        const updateDatasource = () => {
          control.datasource = '';
          //this.ref.tick();
          control.datasource = control.dynamic_datasource?.replace(rx, (_, id) => {
            const value = formGroup.get(id)?.value;
            const text = value && value.value ? value.value : value;
            return text || '';
          });
        };
        while (result = rx.exec(control.dynamic_datasource as string)) {
          const id = result[1];
          if (id) {
            const controlType = this.getControlType(id, stepControls);
            const formControl = formGroup.get(id);
            if ([TheiaControl.Select, TheiaControl.MultiSelect, TheiaControl.Toggle].includes(controlType as TheiaControl)) {
              formControl?.valueChanges
                .subscribe(updateDatasource);
            } else {
              formControl?.valueChanges
                .pipe(distinctUntilChanged(), debounceTime(1000), filter(v => v && v.length > 3),)
                .subscribe(updateDatasource);
            }
          }
        }
        updateDatasource();
      }
    });
  }

  private getControlType(id: string, controls: Control[]): TheiaControl | undefined {
    return this.findControl(id, controls)?.type;
  }

  private findControl(id: string, controls: Control[]): Control | undefined {
    return controls?.find((control) => control.id === id);
  }

}
